import * as React from 'react';
import './Layout.scss';
import { NavLink } from 'react-router-dom';
import * as styles from '../layout/styles';
import { syncer, SyncStatus } from '../db/syncer';
import { cx } from 'emotion';
import { registerThemeListener } from '../layout/theme';

export function isMobile() {
    return window.matchMedia("(max-width: 768px)").matches;
}

export const Layout: React.FC<{}> = (props) => {
    const [sidebar, setSidebar] = React.useState(() => !isMobile())
    const [count, setCount] = React.useState(0);
    const [theme, setTheme] = React.useState<string|null>(null);

    React.useEffect(() => {
        syncer.registerSyncListener(() => setCount(c => c +  1));
    }, []);

    React.useEffect(() => {
        registerThemeListener(x => {
            document.documentElement.className = x;
            setTheme(x)
        });
    }, []);

    function onNavClick() {
        if(isMobile()) {
            setSidebar(false);
        }
    }

    if (theme == null)
    {
        return <></>;
    }

    return <>
        <section style={{display: 'flex', overflow: 'hidden', height: '100%'}} className={theme}>
            <React.Fragment key={count} />
            <div className={cx('navbar')}>
                <div className={cx(styles.horizontalSpace('1rem'))}>
                    {!sidebar && (
                        <>
                            <NavLink className={cx('button', 'hover-glow')} to={ '/' } onClick={onNavClick} exact activeClassName='is-active'>Notes</NavLink>
                            <NavLink className={cx('button', 'hover-glow')} to={ '/tasks' } onClick={onNavClick} exact activeClassName='is-active'>Tasks</NavLink>
                        </>
                    )}
                </div>
                <div className={cx(styles.horizontalSpace('1rem'))}>
                    {syncer.status !== SyncStatus.Idle && <NavLink className="button" to="/sync">Sync: {SyncStatus[syncer.status]}</NavLink>}
                    <button className="button" onClick={() => setSidebar(!sidebar)}>
                        Menu
                    </button>
                </div>
            </div>

            <div className={cx('sidebar', 'list')} style={{overflowY: 'auto', paddingTop: 52, height: '100%', width: sidebar ? undefined : 0, overflowX: 'hidden'}}>
                <NavLink className={cx('item', 'hover-glow')} to={ '/' } onClick={onNavClick} exact activeClassName='is-active'>Notes</NavLink>
                <NavLink className={cx('item', styles.hoverGlow)} to={ '/notes/daily' } onClick={onNavClick} exact activeClassName='is-active'>Daily Notes</NavLink>
                <NavLink className={cx('item', styles.hoverGlow)} to={ '/tasks' } onClick={onNavClick} exact activeClassName='is-active'>Tasks</NavLink>
                <NavLink className={cx('item', styles.hoverGlow)} to={ '/projects' } onClick={onNavClick} exact activeClassName='is-active'>Projects</NavLink>
                <NavLink className={cx('item', styles.hoverGlow)} to={ '/contexts' } onClick={onNavClick} exact activeClassName='is-active'>Contexts</NavLink>
                <NavLink className={cx('item', styles.hoverGlow)} to={ '/people' } onClick={onNavClick} exact activeClassName='is-active'>People</NavLink>
                <NavLink className={cx('item', styles.hoverGlow)} to={ '/playground' } onClick={onNavClick} exact activeClassName='is-active'>Playground</NavLink>
                <NavLink className={cx('item', styles.hoverGlow)} to={ '/stats' } onClick={onNavClick} exact activeClassName='is-active'>Stats</NavLink>
                <NavLink className={cx('item', styles.hoverGlow)} to={ '/settings' } onClick={onNavClick} exact activeClassName='is-active'>Settings</NavLink>
                <NavLink className={cx('item', styles.hoverGlow)} to={ '/sync' } onClick={onNavClick} exact activeClassName='is-active'>Sync Status</NavLink>
            </div>

            <div className="main" style={{paddingTop: 52}}>
                { props.children }
            </div>
        </section>
    </>;
}
