import * as React from 'react';
import { isMobile } from './Layout';
import * as styles from '../layout/styles';
import { cx } from 'emotion';

export const SettingsCompactor : React.FC<{className?: string, alwaysVisible?: () => JSX.Element, mobileOnly?: () => JSX.Element, wideStyle?:React.CSSProperties}> = props => {
    const [expanded, setExpanded] = React.useState(false);

    const alwaysVisible = props.alwaysVisible ? props.alwaysVisible() : null;

    if(!isMobile()) {
        return (
            <div style={{display: 'flex', alignItems: 'center', ...props.wideStyle}} className={styles.horizontalSpace('.3rem')}>
                {alwaysVisible}
                {props.children}
            </div>
        );
    }

    const mobileOnly = props.mobileOnly ? props.mobileOnly() : null;

    if(!expanded) {
        return (
            <div style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between'}}>
                {alwaysVisible}
                {mobileOnly}
                <div className="button" onClick={() => setExpanded(true)}>S</div>
            </div>
        );
    }

    return (
        <div style={{display: 'flex', flexDirection: 'column'}} className={styles.verticalSpace('.3rem')}>
            <div style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between'}}>
                {alwaysVisible}
                <div className="button" onClick={() => setExpanded(false)}>S</div>
            </div>
            <div className={cx(styles.verticalSpace('.3rem'), props.className)}>
                {props.children}
            </div>
        </div>
    );
};