import { Entity } from "./entity";
import moment from "moment";

export enum NoteMode {
    Tree,
    Markdown
};

export interface Note extends Entity {
    type: 'note',
    mode: NoteMode;
    title: string;
    tags: string;
    content: string;
    contextId: string|null;
    noteFor: string|null;
    dailyDate: string|null;
};

export interface HasNote extends Entity {
    noteId: string|null;
}

export function getNoteTitle(note: Pick<Note, 'dailyDate'|'title'|'content'>) {
    if (note.dailyDate) {
        return `Daily - ${moment(note.dailyDate).format('MMMM Do, YYYY')}`;
    }

    if (note.title && note.title.length) {
        return note.title;
    }

    const parsedTitle = /# (.+)/.exec(note.content);

    if (parsedTitle && parsedTitle.length > 1) {
        return parsedTitle[1];
    }

    return '';
}

export function getNoteTitleType(note: Note): 'daily'|'title'|'parsed'|'none' {
    if (note.dailyDate) {
        return 'daily';
    }

    if (note.title && note.title.length) {
        return 'title';
    }

    const parsedTitle = /# (.+)/.exec(note.content);

    if (parsedTitle && parsedTitle.length > 1) {
        return 'parsed';
    }

    return 'none';
}

export const noteReferenceRegex = /\[\[([^\]]+)\]\]/ig;

function escapeRegExp(str: string) {
    return str.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'); // $& means the whole matched string
}

export function getNoteContextTitlePrefixRegExp(contextTitle: string) {
    return new RegExp(`^${escapeRegExp(contextTitle)}/`, 'i')
}

export function getNoteTitleMatchRegExp(reference: string) {
    return new RegExp(`^${escapeRegExp(reference)}$`, 'i')
}

export function getNoteReferenceMatchRegExp(reference: string) {
    return new RegExp(`\\[\\[${escapeRegExp(reference)}\\]\\]`, 'i')
}

export function getNoteAliasReferenceMatchRegExp(alias: string) {
    return new RegExp(`\\B@${escapeRegExp(alias)}`, 'i')
}

export function extractReferences(text: string) {
    if(!text) {
        return [];
    }

    const regex = /\[\[([^\]]+)\]\]/ig;
    const results = [];
    for(const match of text.matchAll(regex)) {
        results.push(match[1])
    }

    return results;
}

export function extractAllReferences(notes: Note[]) {
    const results: { [reference: string]: { title: string, contextId: string|null, updatedAt: Date } } = {};

    function getKey(title: string, contextId: string|null) {
        return `${contextId}-${title.toLowerCase()}`
    }

    const existing = new Set<string>(notes.filter(n => n.title).map(n => getKey(n.title, n.contextId)));

    for(const note of notes) {
        const references = extractReferences(note.content);

        for(const reference of references) {
            const key = getKey(reference, note.contextId);
            
            if (existing.has(key)) {
                continue;
            }
            
            const existingResult = results[key];

            if (existingResult && existingResult.updatedAt > new Date(note.updatedAt)) {
                continue;
            }

            results[key] = {
                title: reference,
                contextId: note.contextId,
                updatedAt: new Date(note.updatedAt)
            };
        }
    }

    return Object.values(results);
}