import * as React from 'react';
import { ContextBar } from '../../components/ContextBar';
import { useQueryState } from '../../lib/UseQueryState';
import { ToolSection } from '../../framework/ToolSection';
import { useData } from '../../model';
import moment from 'moment';
import { getNoteTitle, Note, NoteMode } from '../../db/note';
import { ContextTag } from '../../components/ContextTag';
import { MarkedViewer } from '../../components/MarkedViewer';
import _ from 'lodash';
import * as ContextActions from '../../lib/contextActions';
import { TreeViewer } from '../../components/TreeViewer';
import { NavLink } from 'react-router-dom';
import { horizontalSpace } from '../../layout/styles';

export const DailyNotePage: React.SFC<{ }> = (props) => {
    const [contextId, setContextId] = useQueryState(null, 'context');
    const {data} = useData();

    const oneMonthAgo = moment().add(-30,'d');
    
    const filteredContexts = ContextActions.getFilteredContextIds(contextId, data);
    const dailyNotes = _.orderBy(data.notes.filter(x => x.dailyDate && moment(x.dailyDate) > oneMonthAgo && (!contextId || filteredContexts.indexOf(x.contextId!) !== -1)), n => n.dailyDate,  'desc');

    function renderViewer(note: Note) {
        if (note.mode === NoteMode.Markdown) {
            return <MarkedViewer markdown={note.content} contextId={note.contextId} />;
        } 
          
        return <TreeViewer json={note.content} contextId={note.contextId} />;
    }

    const contextCounts = _.countBy(data.notes.filter(n => n.dailyDate), x  => x.contextId);
    const contexts = _.orderBy(Object.keys(contextCounts).map(k => ({ contextId: k, count: contextCounts[k], context: data.contexts.find(x => x.id === k) })), x => x.count, 'desc');

    const urlParams = new URLSearchParams(window.location.search);
    const source = `?source=${encodeURIComponent(`${window.location.pathname}?${urlParams.toString()}`)}`

    return (
        <div style={{display: 'flex', height: '100%'}}>
            <ContextBar contextId={contextId} onChange={setContextId} />
            <div style={{width: '100%'}}>
                <ToolSection>
                    {contexts.map(c => (
                        <NavLink key={c.contextId} to={`/notes/new?context=${c.contextId}&daily`} className="button">New Note{c.context ? ` - ${c.context.title}` : ''}</NavLink>
                    ))}
                </ToolSection>
                <div className="panel">
                    {dailyNotes.map(n => (
                        <div key={n.id} className="content" style={{padding: '1rem'}}>
                            <h1>{getNoteTitle(n)}</h1>
                            <div style={{display: 'flex', justifyContent: 'space-between'}}>
                                <div className={horizontalSpace('8px')}>
                                    <a className="button small" href={`/notes/${n.id}${source}`}>Show</a>
                                    <a className="button small" href={`/notes/${n.id}/edit${source}`}>Edit</a>
                                </div>
                                <ContextTag contextId={n.contextId} />
                            </div>
                            {renderViewer(n)}
                        </div>))}
                </div>
            </div>
        </div>
    )
};
