import * as React from 'react';
import { NavLink } from 'react-router-dom';
import * as styles from '../layout/styles';
import { Data } from '../model';
import { ToolSection } from '../framework/ToolSection';
import { ProjectState, Project } from '../db/project';
import { ContextTag } from '../components/ContextTag';
import { cx } from 'emotion';
import { getVariable } from '../layout/theme';
import { Api } from '../bridge/HostBridge';

export class ProjectPage extends React.Component<{ data: Data, refreshData: () => void }, { text: string }> {
    constructor(props: any) {
        super(props);

        this.state = {
            text: ''
        };
    } 
    
    async onKeyPress(e: React.KeyboardEvent<HTMLInputElement>) {
        if (e.key === 'Enter') {
            await Api.saveProject({
                title: this.state.text,
                state: ProjectState.Active,
                contextId: null
            } as Project);
            this.setState({ text: '' }, this.props.refreshData);
        }
    }

    renderProject(target: Project) {
        return (
            <NavLink key={target.id} to={`/projects/${target.id}`} className={cx('item', styles.hoverGlow)}>
                <div>
                    {target.title}
                </div>
                <div className="tags">
                    <div className="tag">{target.state}</div>
                    <ContextTag contextId={target.contextId} />
                </div>
            </NavLink>
        );
    }

    render() {
        return (
            <>
                <ToolSection>
                    <input type="text" className="input is-large is-wide" placeholder="Add project..." value={this.state.text} onChange={e => this.setState({ text: e.target.value })} onKeyPress={this.onKeyPress.bind(this)} />
                </ToolSection>

                <div className="list">
                    <div className="header" style={{background: getVariable('--task-project-background')}}>Active</div>
                    {this.props.data.projects.filter(p => p.state === ProjectState.Active).sort((a, b) =>  b.title! < a.title! ?  -1 : b.title! > a.title! ? 1 : 0).map(t => this.renderProject(t))}
                    <div className="header"  style={{background: getVariable('--task-project-background')}}>Incubated</div>
                    {this.props.data.projects.filter(p => !p.state || p.state === ProjectState.Incubated).sort((a, b) =>  b.title! < a.title! ?  -1 : b.title! > a.title! ? 1 : 0).map(t => this.renderProject(t))}
                    <div className="header"  style={{background: getVariable('--task-project-background')}}>Not Active</div>
                    {this.props.data.projects.filter(p => p.state !== ProjectState.Active && p.state !== ProjectState.Incubated).sort((a, b) =>  b.title! < a.title! ?  -1 : b.title! > a.title! ? 1 : 0).map(t => this.renderProject(t))}
                </div>
            </>
        );
    }
}
