import * as React from 'react';
import { Route, Switch } from 'react-router-dom';
import { Layout } from './components/Layout';
import { TaskPage } from './pages/TaskPage';
import { EditNotePage } from './pages/notes/EditNotePage';
import { ShowNotePage, ShowVirtualNotePage } from './pages/notes/ShowNotePage';
import { NotePage } from './pages/notes/NotePage';
import { PlaygroundPage } from './pages/PlaygroundPage';
import { ContextPage } from './pages/ContextPage';
import { ProjectPage } from './pages/ProjectPage';
import { ShowTaskPage } from './pages/ShowTaskPage';
import { ShowProjectPage } from './pages/ShowProjectPage';
import { ShowContextPage } from './pages/ShowContextPage';
import { DataPage } from './pages/DataPage';
import { SyncPage } from './pages/SyncPage';
import { SettingsPage } from './pages/SettingsPage';
import { PeoplePage } from './pages/people/PeoplePage';
import { EditPersonPage } from './pages/people/EditPersonPage';
import { ShowPersonPage } from './pages/people/ShowPersonPage';
import { StatsPage } from './pages/StatsPage';
import {DataProvider} from './model';
import { DailyNotePage } from './pages/notes/DailyNotes';
import './theme.scss';

export const routes = <Layout>
    <DataProvider>
        <Switch>
            <Route exact path='/' render={routeProps => <NotePage />} />
            <Route exact path='/notes/new' render={routeProps => <EditNotePage />} />
            <Route exact path='/notes/daily' render={routeProps => <DailyNotePage />} />
            <Route exact path='/notes/virtual' render={routeProps => <ShowVirtualNotePage />} />
            <Route exact path='/notes/:id' render={routeProps => <ShowNotePage id={routeProps.match.params.id} />} />
            <Route exact path='/notes/:id/edit' render={routeProps => <EditNotePage id={routeProps.match.params.id} />} />
            <Route exact path='/tasks' render={routeProps => <TaskPage />} />
            <Route exact path='/tasks/:id' render={routeProps => <DataPage render={(props) => <ShowTaskPage {...props} id={routeProps.match.params.id} />} />} />
            <Route exact path='/contexts' render={routeProps => <DataPage render={(props) => <ContextPage {...props} />}  />} />
            <Route exact path='/contexts/:id' render={routeProps => <DataPage render={(props) => <ShowContextPage {...props} id={routeProps.match.params.id} />} />} />
            <Route exact path='/projects' render={routeProps => <DataPage render={(props) => <ProjectPage {...props} />} />} />
            <Route exact path='/projects/:id' render={routeProps => <DataPage render={(props) => <ShowProjectPage {...props} id={routeProps.match.params.id} />} />} />
            <Route exact path='/people' render={routeProps => <PeoplePage />} />
            <Route exact path='/people/new' render={routeProps => <EditPersonPage />} />
            <Route exact path='/people/:id' render={routeProps => <ShowPersonPage id={routeProps.match.params.id} />} />
            <Route exact path='/people/:id/edit' render={routeProps => <EditPersonPage id={routeProps.match.params.id} />} />
            <Route path='/playground' component={PlaygroundPage} />
            <Route path='/sync' component={SyncPage} />
            <Route path='/stats' render={routeProps => <StatsPage />} />
            <Route path='/settings' component={SettingsPage} />
        </Switch>
    </DataProvider>
</Layout>;
