import {createInstance} from 'localforage';
import { EventListener } from '../db/EventListener';

const themeStore = createInstance({ name: 'theme' });
const themeListener = new EventListener<string>();

const defaultTheme = 'dark-theme';
let currentTheme = defaultTheme;

async function init() {
    currentTheme = await themeStore.getItem<string>('theme');
    currentTheme = currentTheme || defaultTheme;
    themeListener.trigger(currentTheme);
}

init();

export async function setTheme(theme: string) {
    currentTheme = theme;
    await themeStore.setItem('theme', currentTheme);
    themeListener.trigger(currentTheme);
}

export function registerThemeListener(listener: (theme: string) => void) {
    themeListener.register(listener)
}

export function getVariable(variable: string) {
    return getComputedStyle(document.documentElement).getPropertyValue(variable);
}

export function isDark() {
    return currentTheme === defaultTheme;
}

