import * as React from 'react';
import { ToolSection } from '../framework/ToolSection';
import { SimpleSplitEditor, SplitMode, SplitModeSelector, NoteModeSelector } from './SimpleSplitEditor';
import { isMobile } from './Layout';
import { getNoteTitle, Note, NoteMode } from '../db/note';
import { TreeEditor } from './TreeEditor';
import { SettingsCompactor } from './SettingsCompactor';
import './NoteEditor.scss';
import { ContextSelector } from '../components/ContextSelector';
import { Data } from '../model';
import moment from 'moment';
import { TagInput } from './TagInput';

export function NoteEditor(props: {note: Note, updateNote: (props: Partial<Note>) => void, onNoteAdded: () => void, availableTags: string[], data: Data}) {
    const [splitMode, setSplitMode] = React.useState(isMobile() ? SplitMode.Editor : SplitMode.Split)

    const updateDailyDate = (value: string) => {
        if (value === '') {
            props.updateNote({ dailyDate: null });
        } else {
            props.updateNote({ dailyDate: new Date(value).toISOString() });
        }
    }

    return (
        <div style={{width: '100%', flex: 1, display: 'flex', flexDirection: 'column'}}>
            <ToolSection verticalAround={false}>
                <SettingsCompactor wideStyle={{justifyContent: 'space-between'}} alwaysVisible={() => (
                    <>
                        <ContextSelector contextId={props.note.contextId} data={props.data} onChange={contextId => props.updateNote({ contextId })} />
                        <input type="date" className="input is-large" value={(props.note.dailyDate && moment(props.note.dailyDate).format('YYYY-MM-DD')) || ''} onChange={e => updateDailyDate(e.target.value)} />
                    </>
                )}>
                    <NoteModeSelector mode={props.note.mode || NoteMode.Tree} onModeChange={mode => props.updateNote({ mode })} />
                    <SplitModeSelector mode={splitMode} onModeChange={splitMode => setSplitMode(splitMode)} />
                </SettingsCompactor>
            </ToolSection>
            <ToolSection>
                {!props.note.noteFor && !props.note.dailyDate && (
                    <>
                        <input type="text" placeholder="Title" className="input is-large is-wide" value={props.note.title} onChange={e => props.updateNote({ title: e.target.value })} />
                        <TagInput availableTags={props.availableTags} tags={props.note.tags} onChange={tags => props.updateNote({ tags })} />
                    </>
                )}
                {props.note.dailyDate && (
                    <h1>{getNoteTitle({ dailyDate: props.note.dailyDate, title: props.note.title, content: props.note.content })}</h1>
                )}
            </ToolSection>
            <div style={{flex: 1}}>
                {props.note.mode === NoteMode.Markdown ? (
                    <SimpleSplitEditor text={props.note.content} expand={true} onTextChanged={content => props.updateNote({ content })} mode={splitMode} data={props.data} contextId={props.note.contextId} />
                ) : (
                    <TreeEditor text={props.note.content} onTextChanged={content => props.updateNote({ content })} contextId={props.note.contextId} />
                )}
            </div>
        </div>
    );
}