import * as React from 'react';
import { NavLink } from 'react-router-dom';
import { Data } from '../model';
import { ToolSection } from '../framework/ToolSection';
import { Context, ContextState } from '../db/context';
import { Api } from '../bridge/HostBridge';

export class ShowContextPage extends React.Component<{id: string, data: Data, refreshData(): void }, { title: string }> {

    constructor(props: any) {
        super(props);

        this.state = {
            title: '',
        }
    }

    componentDidMount() {
        let target = this.props.data.contexts.find(x => x.id === this.props.id);
        this.setState({ title: target!.title })
    }

    async onKeyDown(e: React.KeyboardEvent<HTMLInputElement>) {
        if (e.key === 'Enter') {
            const target = this.props.data.contexts.find(x => x.id === this.props.id);

            if (!target) {
                return;
            }

            if (target.title !== this.state.title) {
                target.title = this.state.title;
                await Api.saveContext(target);
                this.props.refreshData();
            }
        }
    }

    async setContext(target: Context, event: React.ChangeEvent<HTMLSelectElement>) {
        let contextId: string|null = event.target.value;

        if (contextId === '') {
            contextId = null;
        }

        target.contextId = contextId;
        await Api.saveContext(target);
        this.props.refreshData();
    }

    async setContextState(target: Context, event: React.ChangeEvent<HTMLSelectElement>) {
        let contextState: ContextState = event.target.value as unknown as ContextState;

        target.state = contextState;
        await Api.saveContext(target);
        this.props.refreshData();
    }

    render() {
        let target = this.props.data.contexts.find(x => x.id === this.props.id);

        if (!target) {
            return <div>Context not found.</div>
        }

        let isDirty = false;

        if (target.title !== this.state.title) {
            isDirty = true;
        }

        return (
            <>
                <ToolSection>
                    <NavLink to={`/contexts`} className="button">Back</NavLink>
                </ToolSection>

                <ToolSection>
                    <div>Context</div>
                    <input type="text" className="input is-large is-wide" style={{border: isDirty ? '2px solid orange' : undefined}} value={this.state.title} onKeyDown={e => this.onKeyDown(e)} onChange={e => this.setState({ title: e.target.value })} />

                    <div>Parent Context</div>
                    <select className="input is-large" value={target.contextId || ''} onChange={e => this.setContext(target!, e)}>
                        <option value="">Not set</option>
                        {this.props.data.contexts.filter(c => c.id !== target!.id).map(c => (
                            <option key={c.id} value={c.id}>{c.title}</option>
                        ))}
                    </select>

                    <div>State</div>
                    <select className="input is-large" value={target.state || ContextState.Active} onChange={e => this.setContextState(target!, e)}>
                        {Object.keys(ContextState).filter(c => !isNaN(c as any)).map(c => (
                            <option key={c} value={c}>{ContextState[c as any]}</option>
                        ))}
                    </select>
                </ToolSection>
            </>
        );
    }
}