import * as React from 'react';
import { NavLink, Redirect } from 'react-router-dom';
import { ToolSection } from '../../framework/ToolSection';
import { useState } from 'react';
import { EmbeddedNoteEdit } from '../../components/EmbeddedNote';
import { useData } from '../../model';
import { Api } from '../../bridge/HostBridge';
import { Person } from '../../db/person';

const useForm = <T extends {}>(initialState: ()=>T) => {
    const [values, setValues] = useState<T>(initialState);
    
    const handleChange = (event: any) => {
        event.persist();
        setValues((values) => ({ ...values, [event.target.name]: event.target.value }));
    };

    return {
        values,
        handleChange
    };
};

export const EditPersonPage: React.FC<{id?: string }> = (props) => {
    const {data} = useData();
    const [goBack, setGoBack] = useState<string|null>(null);

    const {values, handleChange} = useForm(() => {
        const person = props.id ? data.people.find(x => x.id === props.id) : null;
        return {
            name:  person ? person.name : '',
            alias: person ? person.alias : '',
            birthday: person ? person.birthday : null
        }
    });

    const onSave = async () => {
        const id = props.id || null;

        if (id) {
            let person = data.people.find(p => p.id === id)!;

            if (person === null) {
                throw new Error('Person is null?');
            }

            person.name = values.name;
            person.alias = (values.alias?.length && values.alias) || null;
            person.birthday = values.birthday;

            await Api.savePerson(person);
            setGoBack(person.id);
        } else {
            await Api.savePerson({
                name: values.name,
                alias: (values.alias?.length && values.alias) || null,
                birthday: values.birthday,
                noteId: null,
            } as Person);

            // setGoBack(person.id);
        }
    };

    const person = props.id ? data.people.find(x => x.id === props.id) || null : null;

    return (
        <>
            {goBack && <Redirect to={`/people/${goBack}`} />}
            <div style={{height: '100%', display: 'flex', flexDirection: 'column'}} >
                <ToolSection>
                    <NavLink to={`/people`} className="button">Back</NavLink>
                    {props.id && <NavLink to={`/people/${props.id}`} className="button">Show</NavLink>}
                </ToolSection>
                <ToolSection>
                    {props.id === undefined ?
                            <button type="button" className="button is-primary" onClick={onSave}>Add person</button> :
                            <button type="button" className="button is-primary" onClick={onSave}>Update person</button>}
                </ToolSection>
                <ToolSection>
                    <div>Name</div>
                    <input type="text" name="name" className="input is-large is-wide" value={values.name} onChange={handleChange} />
                    <div>Alias</div>
                    <input type="text" name="alias" className="input is-large is-wide" value={values.alias || ''} onChange={handleChange} />
                    <div>Birthday</div>
                    <input type="date" name="birthday" className="input is-large" value={values.birthday || ''} onChange={handleChange} />
                </ToolSection>
                <ToolSection>
                    <EmbeddedNoteEdit targetEntity={person} type={'person'} />
                </ToolSection>
            </div>
        </>
    );
};