import * as React from 'react';
import { useData } from '../model';
import { Person } from '../db/person';
import { NavLink } from 'react-router-dom';
import { MarkedViewer } from './MarkedViewer';
import { Task } from '../db/task';
import { HasNote, Note, NoteMode } from '../db/note';
import { TreeViewer } from './TreeViewer';
import { Api } from '../bridge/HostBridge';

export function EmbeddedNoteEdit(props: { type:'person'|'task', targetEntity: Person|Task|null }) {
    const {data, refresh} = useData();

    if (!props.targetEntity) {
        return null;
    }

    const {noteId} = props.targetEntity;

    const addNode = async () => {
        if (!props.targetEntity) {
            return;
        }

        const note = await Api.saveNote({
            title: '',
            content: '',
            tags: '',
            contextId: null,
            noteFor: props.targetEntity.id,
            mode: NoteMode.Tree,
            dailyDate: null
        } as Note);

        if (props.type === 'person') {
            props.targetEntity.noteId = note.id;
            await Api.savePerson(props.targetEntity as Person);
        }

        if (props.type === 'task') {
            props.targetEntity.noteId = note.id;
            await Api.saveTask(props.targetEntity as Task);
        }

        refresh();
    };

    if (!noteId) {
        return (
            <div>
                <div className="button" onClick={addNode}>Add note</div>
            </div>
        );
    }

    const note = data.notes.find(x => x.id === noteId);

    if (!note) {
        return null;
    }

    return (
        <div>
            <NavLink to={`/notes/${note.id}/edit?source=${encodeURIComponent(`${window.location.pathname}`)}`} className="button">Edit note</NavLink>
            {note.mode === NoteMode.Markdown ? (
                <MarkedViewer markdown={note.content} contextId={note.contextId} />
            ) : (
                <TreeViewer json={note.content} contextId={note.contextId} />
            )}
        </div>
    )
};

export const EmbeddedNoteShow: React.FC<{ targetEntity: HasNote|null }> = (props) => {
    const {data} = useData();

    if (!props.targetEntity) {
        return null;
    }

    const {noteId} = props.targetEntity;
    if (!noteId) {
        return null;
    }

    const note = data.notes.find(x => x.id === noteId);

    if (!note) {
        return null;
    }

    return (
        <div>
            {note.mode === NoteMode.Markdown ? (
                <MarkedViewer markdown={note.content} contextId={note.contextId} />
            ) : (
                <TreeViewer json={note.content} contextId={note.contextId} />
            )}
        </div>
    )
};