import * as React from 'react';
import { NavLink } from 'react-router-dom';
import * as styles from '../layout/styles';
import { useData } from '../model';
import { ToolSection } from '../framework/ToolSection';
import * as ContextActions from '../lib/contextActions';
import { isMobile } from '../components/Layout';
import { Task, TaskState } from '../db/task';
import { ProjectState, Project } from '../db/project';
import moment from 'moment';
import { useQueryState } from '../lib/UseQueryState';
import { SettingsCompactor } from '../components/SettingsCompactor';
import { ContextTag } from '../components/ContextTag';
import { ContextBar } from '../components/ContextBar';
import { cx } from 'emotion';
import _ from 'lodash';
import { getVariable } from '../layout/theme';
import { Api } from '../bridge/HostBridge';

interface Perspective {
    id: string;
    title: string;
    groupedProject: boolean;
    groupedContext: boolean;
}

let perspectives: Perspective[] = [
    { id: '1', title: 'All', groupedProject: false, groupedContext: false },
    { id: '2', title: 'Grouped', groupedProject: false, groupedContext: true },
    { id: '3', title: 'Grouped Project', groupedProject: true, groupedContext: false }
]

let defaultPerspective = perspectives[1];

interface TaskFilters {
    showCompleted: boolean;
    showContextBar: boolean;
    includeNested: boolean;
    showDeferred: boolean;
    showIncubated: boolean;
}

export const TaskPage: React.FC<{}> = (props) => {
    const {data, refresh} = useData();
    const [text, setText] = React.useState('');
    const [options, setOptions] = React.useState<TaskFilters>({
        showCompleted: false,
        showDeferred: false,
        showIncubated: false,
        showContextBar: isMobile() ? false : true,
        includeNested: true,
    });

    const [contextId, setContextId] = useQueryState(null, 'context');
    const [projectId, setProjectId] = useQueryState(null, 'project');
    const [perspectiveId, setPerspectiveId] = useQueryState('2', 'perspective');

    const onKeyPress = async (e: React.KeyboardEvent<HTMLInputElement>) => {
        if (e.key === 'Enter') {
            await Api.saveTask({
                title: text,
                contextId: contextId,
                projectId: projectId,
                content: null,
                state: TaskState.Active,
                stateChangedAt: new Date().toISOString(),
                noteId: null,
            } as Task);

            setText('');

            refresh();
        }
    }

    const onCompleteChange = async (task: Task, e: React.ChangeEvent<HTMLInputElement>) => {
        task.state = task.state === TaskState.Completed ? TaskState.Active : TaskState.Completed;
        task.stateChangedAt = new Date().toISOString();
        await Api.saveTask(task);
        refresh();
    }

    const renderProject = (task: Task) => {
        const project = data.projects.find(p => p.id === task.projectId);

        if (project) {
            return <div className="tag is-info">{project.title}</div>;
        }

        return null;
    };

    const renderDeferred = (task: Task) => {
        if (task.deferredUntil && new Date(task.deferredUntil) > new Date()) {
            return <div className="tag is-primary" title={`${moment(task.deferredUntil).format('YYYY-MM-DD')}`}>deferred {moment(task.deferredUntil).fromNow(true)}</div>;
        }
        return null;
    };

    const renderNote = (task: Task) => {
        if (task.noteId) {
            return <div className="tag is-primary" >N</div>;
        }
        return null;
    };

    const renderTitle = (title: string) => {
        const pattern = /(https?:\/\/[^\s]+)/;
        return title.split(pattern).map((c, i) => {
            if (c.match(pattern)) {
                let title = c.replace(/https?:\/\//, '');
                title = title.replace(/\/.*/, '/...');
                return <a key={i} href={c} onClick={e => { e.stopPropagation(); }} target="_blank"  rel="noopener noreferrer" className="button is-small is-link" style={{display: 'inline-block', marginLeft: '5px', marginRight: '5px'}}>{title}</a>;
            }

            return <span key={i}>{c}</span>;
        });
    };

    const filterTasks = (complete: boolean, contexts: ContextActions.NestedContexts) => {
        let tasks = data.tasks;

        if (complete) {
            tasks = tasks.filter(t => t.state === TaskState.Completed)
        } else {
            tasks = tasks.filter(t => t.state !== TaskState.Completed)
        }

        if (projectId) {
            tasks = tasks.filter(t => t.projectId === projectId);
        }

        if (!options.showDeferred) {
            tasks = tasks.filter(t => t.deferredUntil == null || new Date(t.deferredUntil) < new Date());
        }

        if (!options.showIncubated) {
            tasks = tasks.filter(t => t.state !== TaskState.Incubated);

            const incubatedProjectIds = data.projects.filter(p => p.state === ProjectState.Incubated).map(p => p.id);
            tasks = tasks.filter(t => !t.projectId || incubatedProjectIds.indexOf(t.projectId) === -1);
        }

        if (contextId) {
            const contextIds = ContextActions.getFilteredContextIds(contextId, data);
            tasks = tasks.filter(t => t.contextId && contextIds.indexOf(t.contextId) !== -1);
        }

        return tasks.sort((a, b) =>  b.createdAt! < a.createdAt! ?  -1 : b.createdAt! > a.createdAt! ? 1 : 0);
    }

    const renderTask = (task: Task, skipProjectContext = false) => {
        let source: string|null = null;
        if (window.location.search.length) {
            const urlParams = new URLSearchParams(window.location.search);
            source = `?source=${encodeURIComponent(`${window.location.pathname}?${urlParams.toString()}`)}`
        }

        return (
            <NavLink key={task.id} to={`/tasks/${task.id}${source ? source : ''}`} className={cx('item', 'task', styles.hoverGlow)}>
                <div className="task-checkbox">
                    <input type="checkbox" style={{display: 'block', padding: 10}} checked={task.state === TaskState.Completed} onClick={e => e.preventDefault()} onChange={e => onCompleteChange(task, e)}/>
                </div>
                <div className="task-title" style={{color: task.state === TaskState.Incubated ? '#999' : undefined}}>
                    {task.state === TaskState.Completed ? <span style={{textDecoration: 'line-through'}}>{renderTitle(task.title)}</span> : renderTitle(task.title)}
                    {task.state === TaskState.Incubated ? <span> [Incubated]</span> : null}
                </div>
                <div className="task-details">
                    <div className="tags">
                        {renderNote(task)}
                        {renderDeferred(task)}
                        {!skipProjectContext && <ContextTag contextId={task.contextId} /> }
                        {!skipProjectContext && renderProject(task)}
                    </div>
                </div>
            </NavLink>
        );
    };

    const perspective = perspectives.find(x => x.id === perspectiveId) || defaultPerspective;
    const nestedContexts = ContextActions.getNestedContexts(data);

    let taskBlock: JSX.Element[] = [];

    if (perspective.groupedContext) {
        const filteredContextIds: (string|null)[] = ContextActions.getFilteredContextIds(contextId, data);

        if (!contextId) {
            filteredContextIds.push(null);
        }

        for (const contextId of filteredContextIds) {
            const context = data.contexts.find(p => p.id === contextId);

            let tasks = filterTasks(false, nestedContexts);

            if (contextId) {
                if (!context) {
                    continue;
                }

                tasks = tasks.filter(t => t.contextId === contextId);

                if (!tasks.length) {
                    continue;
                }

                taskBlock.push(
                    <div key={contextId} style={{display: 'flex', justifyContent: 'space-between', padding: '.6rem', background: getVariable('--task-context-background'), cursor: 'pointer'}} onClick={() => setContextId(context.id)}>
                        {context.title}
                        <NavLink className="button small" to={`/contexts/${context.id}`}>Edit</NavLink>
                    </div>
                )
            } else {
                tasks = tasks.filter(t => t.contextId === contextId);

                if (!tasks.length) {
                    continue;
                }

                taskBlock.push(<div key={'no-context'} style={{padding: '.6rem', background: getVariable('--task-context-background')}}>No Context</div>)
            }

            let stateOrder = [ProjectState.Active, ProjectState.Incubated, ProjectState.Completed, ProjectState.Dropped];
    
            const emptyActiveProjects: Project[] = [];
    
            let filteredProjects = data.projects;
    
            if (projectId !== null) {
                filteredProjects = filteredProjects.filter(p => p.id === projectId);
            }
    
            filteredProjects.sort((a, b) => stateOrder.indexOf(a.state) - stateOrder.indexOf(b.state)).forEach(p => {
                let projectTasks = tasks.filter(t => t.projectId === p.id);
    
                if (!projectTasks.length) {
                    if (p.state === ProjectState.Active) {
                        if (contextId) {
                            if (p.contextId && filteredContextIds.indexOf(p.contextId) !== -1) {
                                emptyActiveProjects.push(p);
                            }
                        } else {
                            emptyActiveProjects.push(p);
                        }
                    }
                    return;
                }
    
                taskBlock.push(
                    <React.Fragment key={`p-${p.id}`}>
                        <div className="header link" onClick={() => { setProjectId(p.id); setContextId(p.contextId); }} style={{background: getVariable('--task-project-background')}}>
                            {p.title} {p.state !== ProjectState.Active ? `[${p.state}]` : ''}
                            <div style={{display: 'flex'}}>
                                <NavLink className="button small" to={`/projects/${p.id}`}>Edit</NavLink>
                            </div>
                        </div>
                        {projectTasks.map(t => renderTask(t, true))}
                    </React.Fragment>
                );
            });
    
            const noProjectTasks = tasks.filter(t => t.projectId === null);
    
            if (noProjectTasks.length) {
                taskBlock.push(
                    <React.Fragment key={`no-p-${contextId}`}>
                        <div className="header" style={{background: getVariable('--task-project-background')}}>No Project</div>
                        {noProjectTasks.map(t => renderTask(t, true))}
                    </React.Fragment>
                );
            }
    
            for(const p of emptyActiveProjects) {
                if (p.contextId !== contextId) {
                    continue;
                }

                taskBlock.push(
                    <div key={p.id} className="header link" style={{background: getVariable('--task-project-background')}} onClick={() => { setProjectId(p.id); setContextId(p.contextId); }}>
                        {p.title} {p.state !== ProjectState.Active ? `[${p.state}]` : ''} [Empty]
                        <div style={{display: 'flex'}}>
                            <div className="tags">
                                <ContextTag contextId={p.contextId} />
                            </div>
                            <NavLink className="button small" to={`/projects/${p.id}`}>Edit</NavLink>
                        </div>
                    </div>
                );
            }
        }

        if (options.showCompleted) {
            taskBlock.push(
                <React.Fragment key="completed">
                    <div className="header">Completed</div>
                    {_.orderBy(filterTasks(true, nestedContexts), x => x.stateChangedAt, 'desc').map(t => renderTask(t))}
                </React.Fragment>
            );
        }

    } else if (perspective.groupedProject) {
        let tasks = filterTasks(false, nestedContexts);

        let stateOrder = [ProjectState.Active, ProjectState.Incubated, ProjectState.Completed, ProjectState.Dropped];

        const emptyActiveProjects: Project[] = [];

        let filteredProjects = data.projects;

        if (projectId !== null) {
            filteredProjects = filteredProjects.filter(p => p.id === projectId);
        }

        const filteredContextIds = ContextActions.getFilteredContextIds(contextId, data);

        filteredProjects.sort((a, b) => stateOrder.indexOf(a.state) - stateOrder.indexOf(b.state)).forEach(p => {
            let projectTasks = tasks.filter(t => t.projectId === p.id);

            if (!projectTasks.length) {
                if (p.state === ProjectState.Active) {
                    if (contextId) {
                        if (p.contextId && filteredContextIds.indexOf(p.contextId) !== -1) {
                            emptyActiveProjects.push(p);
                        }
                    } else {
                        emptyActiveProjects.push(p);
                    }
                }
                return;
            }

            taskBlock.push(
                <React.Fragment key={`p-${p.id}`}>
                    <div className="header link" onClick={() => { setProjectId(p.id); setContextId(p.contextId); }}>
                        {p.title} {p.state !== ProjectState.Active ? `[${p.state}]` : ''}
                        <div style={{display: 'flex'}}>
                            <div className="tags">
                                <ContextTag contextId={p.contextId} />
                            </div>
                            <NavLink className="button small" to={`/projects/${p.id}`}>Edit</NavLink>
                        </div>
                    </div>
                    {projectTasks.map(t => renderTask(t))}
                </React.Fragment>
            );
        });

        const noProjectTasks = tasks.filter(t => t.projectId === null);

        if (noProjectTasks.length) {
            taskBlock.push(
                <React.Fragment key="no-p">
                    <div className="header">No Project</div>
                    {noProjectTasks.map(t => renderTask(t))}
                </React.Fragment>
            );
        }

        for(const p of emptyActiveProjects) {
            taskBlock.push(
                <React.Fragment key={`p-${p.id}`}>
                    <div className="header link" onClick={() => { setProjectId(p.id); setContextId(p.contextId); }}>
                        {p.title} {p.state !== ProjectState.Active ? `[${p.state}]` : ''} [Empty]
                        <div style={{display: 'flex'}}>
                            <div className="tags">
                                <ContextTag contextId={p.contextId} />
                            </div>
                            <NavLink className="button small" to={`/projects/${p.id}`}>Edit</NavLink>
                        </div>
                    </div>
                </React.Fragment>
            );
        }

        if (options.showCompleted) {
            taskBlock.push(
                <React.Fragment key="completed">
                    <div className="header">Completed</div>
                    {filterTasks(true, nestedContexts).map(t => renderTask(t))}
                </React.Fragment>
            );
        }
    } else {
        taskBlock.push(
            <React.Fragment key="tasks">
                {filterTasks(false, nestedContexts).map(t => renderTask(t))}
                {options.showCompleted && <>
                    <div className="header">Completed</div>
                    {filterTasks(true, nestedContexts).map(t => renderTask(t))}
                </>}
            </React.Fragment>
        );
    }

    return (
        <div style={{display: 'flex', height: '100%'}}>
            {options.showContextBar && <ContextBar contextId={contextId} onChange={setContextId} showTaskCount={true} />}
            <div style={{width: '100%'}}>
                <ToolSection>
                    <SettingsCompactor alwaysVisible={() => (
                        <div className="button" onClick={() => { setContextId(null); setProjectId(null); }}>R</div>
                    )} mobileOnly={() => (
                        <div className="button" style={{marginLeft: 'auto', marginRight: 8}} onClick={() => setOptions({ ...options, showContextBar: !options.showContextBar })}>C</div>
                    )}>
                        <select style={{padding: '0.3rem', display: 'block'}} value={contextId || ''} onChange={e => setContextId(e.target.value === '' ? null : e.target.value) }>
                            <option value="">All contexts</option>
                            {data.contexts.map(c => (
                                <option key={c.id} value={c.id}>{c.title}</option>
                            ))}
                        </select>
                        <select style={{padding: '0.3rem', display: 'block'}} value={projectId || ''} onChange={e => setProjectId(e.target.value === '' ? null : e.target.value)}>
                            <option value="">All projects</option>
                            {data.projects.filter(c => c.state === ProjectState.Active).map(c => (
                                <option key={c.id} value={c.id}>{c.title}</option>
                            ))}
                        </select>
                        <select style={{padding: '0.3rem', display: 'block'}} value={perspectiveId || ''} onChange={e => setPerspectiveId(e.target.value === '' ? null : e.target.value)}>
                            <option value="">Select Perspective</option>
                            {perspectives.map(c => (
                                <option key={c.id} value={c.id}>{c.title}</option>
                            ))}
                        </select>
                        <div>
                            <input id="completed-box" type="checkbox" checked={options.showCompleted} onChange={() => setOptions({ ...options, showCompleted: !options.showCompleted })} />
                            <label htmlFor="completed-box">Show completed</label>
                        </div>
                        <div>
                            <input id="deferred-box" type="checkbox" checked={options.showDeferred} onChange={() => setOptions({ ...options, showDeferred: !options.showDeferred })} />
                            <label htmlFor="deferred-box">Show deferred</label>
                        </div>
                        <div>
                            <input id="incubated-box" type="checkbox" checked={options.showIncubated} onChange={() => setOptions({ ...options, showIncubated: !options.showIncubated })} />
                            <label htmlFor="incubated-box">Show incubated</label>
                        </div>
                        <div>
                            <input id="context-box" type="checkbox" checked={options.showContextBar} onChange={() => setOptions({ ...options, showContextBar: !options.showContextBar })} />
                            <label htmlFor="context-box">Show context bar</label>
                        </div>
                        <div>
                            <input id="include-nested-box" type="checkbox" checked={options.includeNested} onChange={() => setOptions({ ...options, includeNested: !options.includeNested })} />
                            <label htmlFor="include-nested-box">Include nested contexts</label>
                        </div>
                    </SettingsCompactor>
                    <input type="text" className="input is-large is-wide" placeholder="Add task..." value={text} onChange={e => setText(e.target.value)} onKeyPress={onKeyPress} />
                </ToolSection>
                <div className="list">
                    {taskBlock}
                </div>
            </div>
        </div>
    );
};
