import * as React from 'react';
import { Data } from '../model';
import { ToolSection } from '../framework/ToolSection';
import { syncer } from '../db/syncer';
import { Loader } from '../components/Loader';

interface DataPageProps {
    data: Data;
    refreshData(): void;
}

export class DataPage extends React.Component<{ render: (props: DataPageProps)=>React.ReactNode }, { data: Data|null, error: string|null }> {

    constructor(props: any) {
        super(props);

        this.state = {
            error: null,
            data: null
        };

        this.refreshData = this.refreshData.bind(this);
    }

    async componentDidMount() {
        syncer.registerDataListener(data => this.setState({ data }));
        this.setState({ data: await syncer.getData() }, async () => {
            await syncer.startServerSync();
            await syncer.ensureServerSyncEnqueued();
        });
    }

    async refreshData() {
        this.setState({ data: await syncer.getData(true) });
    }

    render() {
        if (this.state.error) {
            return (
                <>
                    <div>{this.state.error}</div>
                    <ToolSection>
                        <div className="button" onClick={() => this.refreshData()}>Retry</div>
                        <div className="button" onClick={() => window.location.reload()}>Reload</div>
                        <div className="button" onClick={() => window.location.reload(true)}>Force Reload</div>
                        <a className="button" href="/login" >Login</a>
                    </ToolSection>
                </>
            );
        }      
        
        if (!this.state.data) {
            return <Loader />;
        }

        return this.props.render({ data: this.state.data, refreshData: this.refreshData });
    }
}