import * as React from 'react';
import { MarkedViewer } from './MarkedViewer';
import * as styles from '../layout/styles';
import { NoteMode } from '../db/note';

import AceEditor from "react-ace";
import {textCompleter, snippetCompleter, keyWordCompleter} from "ace-builds/src-noconflict/ext-language_tools";
import "ace-builds/src-noconflict/mode-markdown";
import "ace-builds/src-noconflict/theme-monokai";
import "ace-builds/src-noconflict/theme-textmate";
import { Data } from '../model';
import { cx } from 'emotion';
import { isDark } from '../layout/theme';

export enum SplitMode {
    Split,
    Editor,
    Preview
}

export const NoteModeSelector: React.SFC<{mode: NoteMode, onModeChange: (mode: NoteMode) => void}> = props => (
    <div className={cx(styles.horizontalSpace('.5rem'))}>
        <button className="button" style={{background: props.mode !== NoteMode.Markdown ? '#444' : undefined}} onClick={() => props.onModeChange(NoteMode.Markdown)}>Markdown</button>
        <button className="button" style={{background: props.mode !== NoteMode.Tree ? '#444' : undefined}} onClick={() => props.onModeChange(NoteMode.Tree)}>Tree</button>
    </div>
);


export const SplitModeSelector: React.SFC<{mode: SplitMode, onModeChange: (mode: SplitMode) => void}> = props => (
    <div className={cx(styles.horizontalSpace('.5rem'))}>
        <button className="button" style={{background: props.mode !== SplitMode.Split ? '#444' : undefined}} onClick={() => props.onModeChange(SplitMode.Split)}>Split</button>
        <button className="button" style={{background: props.mode !== SplitMode.Editor ? '#444' : undefined}} onClick={() => props.onModeChange(SplitMode.Editor)}>Editor</button>
        <button className="button" style={{background: props.mode !== SplitMode.Preview ? '#444' : undefined}} onClick={() => props.onModeChange(SplitMode.Preview)}>Preview</button>
    </div>
);

export class SimpleSplitEditor extends React.Component<{ text: string, onTextChanged: (text: string) => void, expand?: boolean, mode: SplitMode, data: Data, contextId: string|null }, {}> {

    render() {
        const style: React.CSSProperties = {};

        if(this.props.expand) {
            style.width = '100%';
            style.height = '100%';
            style.zIndex = 100;
            style.display = 'flex';
            style.maxHeight = '100%';
        }

        const border = '1px solid #666';
        const components = [];

        let textAreaStyle: React.CSSProperties = {
            flexGrow: 1, 
            minWidth: '50%', 
            borderTop: border, 
            height: '100%'
        };

        let markedStyle: React.CSSProperties = {
            flexGrow: 1, 
            minWidth: '50%', 
            background: 'transparent', 
            padding: '1rem', 
            borderTop: border
        };

        if (this.props.mode === SplitMode.Split) {
            textAreaStyle.borderRight = border;
            markedStyle.overflowY = 'scroll';
        }

        if (this.props.mode === SplitMode.Editor || this.props.mode === SplitMode.Split) {
            components.push(
                    <AceEditor 
                        key="editor"
                        value={this.props.text} 
                        fontSize={18}
                        style={textAreaStyle}
                        onChange={e => this.props.onTextChanged(e)} 
                        mode="markdown"
                        theme={isDark() ? "monokai" : 'textmate'}
                        showGutter={false} 
                        showPrintMargin={false}
                        wrapEnabled={true}
                        enableBasicAutocompletion={[snippetCompleter, textCompleter, keyWordCompleter] as any}
                        enableLiveAutocompletion={true}
                        enableSnippets={true}
                    />
            );
        }

        if (this.props.mode === SplitMode.Preview || this.props.mode === SplitMode.Split) {
            components.push(
                <div key="2" style={markedStyle}>
                    <MarkedViewer markdown={this.props.text} contextId={this.props.contextId} />
                </div>
            );
        }

        return (
            <div style={style}>
                {components}
            </div>
        );
    }
}