import * as React from 'react';
import { useState, useRef } from 'react';
import { horizontalSpace, hoverGlow } from '../layout/styles';
import { MarkedViewer } from './MarkedViewer';
import AceEditor, { ICommand } from "react-ace";
import copy from 'copy-to-clipboard';
import {textCompleter, snippetCompleter, keyWordCompleter} from "ace-builds/src-noconflict/ext-language_tools";
import "ace-builds/src-noconflict/mode-markdown";
import "ace-builds/src-noconflict/theme-monokai";
import "ace-builds/src-noconflict/theme-textmate";
import ReactAce from 'react-ace/lib/ace';
import { Loader } from './Loader';
import { extractReferences } from '../db/note';
import { useData } from '../model';
import { cx } from 'emotion';
import { getAlias } from '../db/person';
import { isDark, getVariable } from '../layout/theme';

export interface Node {
    text: string;
    nodes: Node[];
}

export const TreeEditor: React.FC<{text: string, onTextChanged: (text: string) => void, contextId: string|null}> = (props) => {
    const {data} = useData();
    const [invalid] = useState(() => {
        if (!props.text || props.text === '') {
            return false;
        }
        try {
            JSON.parse(props.text);
            return false;
        } catch {
            return true;
        }
    })

    // const [mouseDown, setMouseDown] = useState<Node|null>();
    // const [mouseOver, setMouseOver] = useState<Node|null>();
    const [selectMode, setSelectMode] = useState(false);
    const [selectedNodes, setSelectedNodes] = useState<Node[]>([]);

    const [nodes, setNodes] = useState<Node[]>(() => {
        if (invalid) {
            return [];
        }

        if (!props.text || props.text === '') {
            return [
                { text: '', nodes: [] }
            ];
        }

        return JSON.parse(props.text);
    });

    const inputRef = useRef<ReactAce>(null);
    const [active, setActive] = useState(nodes[0]);

    const [noteTitles, setNoteTitles] = React.useState<{ title: string; context: string|null }[]>();

    React.useEffect(() => {
        const noteCompletions  = data.notes.map(n => {
            let titles = extractReferences(n.content);

            if (n.title && n.title.length) {
                titles.push(n.title);
            }

            const context = data.contexts.find(c => c.id === n.contextId)?.title || null;

            return titles.map(t => ({ title: t, context }));
        }).flat().filter(t => t.title !== null);

        setNoteTitles(noteCompletions);
    }, [props.contextId, data.contexts, data.notes]);

    const [peopleAliases, setPeopleAliases] = React.useState<string[]>();

    React.useEffect(() => {
        const aliases  = data.people.map(n => getAlias(n)).filter(t => t !== null);
        setPeopleAliases(aliases);
    }, [data.people]);

    if (invalid) {
        return (
            <div>Invalid tree content (not valid JSON).</div>
        )
    }

    if (!noteTitles || !peopleAliases) {
        return <Loader />;
    }

    const updateText = () => {
        props.onTextChanged(JSON.stringify(nodes));
    }

    const onInputChanged = (node: Node, value: string) => {
        node.text = value;
        setNodes([...nodes]);
        updateText();
    };

    const getParentNode = (targetNode: Node, searchNodes: Node[] = nodes, parentNode: Node|null = null): Node|null => {
      for(const node of searchNodes) {
          if (node === targetNode) {
              return parentNode;
          }

          const childResult = getParentNode(targetNode, node.nodes, node);
          if (childResult) {
              return childResult;
          }
      }

      return null;
    };

    const preorderTraversal = () => {
        const result: Node[] = [];
        const stack = [...nodes].reverse();

        while(stack.length) {
            let node = stack.pop();

            if (!node) {
                return result;
            }

            result.push(node);

            for(let childNode of [...node.nodes].reverse()) {
                stack.push(childNode);
            }

        }

        return result;
    };

    
    function visitNodes(visit: (n: Node, depth: number, index: number) => void) {
        const stack = nodes.map((node, index) => ({ node, depth: 0, index })).reverse();

        while(stack.length) {
            let next = stack.pop();

            if (!next) {
                return;
            }

            visit(next.node, next.depth, next.index)

            const depth = next.depth;
            for(const childNode of next.node.nodes.map((node, index) => ({ node, depth: depth + 1, index })).reverse()) {
                stack.push(childNode);
            }
        }
    }

    const insertNode = (afterNode: Node, above = false) => {
        const parentNode = getParentNode(afterNode);
        const searchNodes = parentNode ? parentNode.nodes : nodes;
        const nodeIndex = searchNodes.findIndex(x => x === afterNode);
        const node: Node = { text: '', nodes: [] };
        searchNodes.splice(nodeIndex + (above ? 0 : 1), 0, node);
        setNodes([...nodes]);
        return node;
    };

    const removeNode = (node: Node) => {
        const parentNode = getParentNode(node);
        const searchNodes = parentNode ? parentNode.nodes : nodes;
        const nodeIndex = searchNodes.findIndex(x => x === node);
        searchNodes.splice(nodeIndex, 1);
        setNodes([...nodes]);
        return node;
    };

    const removeNodeIfEmpty = (node: Node) => {
        if (node.text.length || node.nodes.length) {
            return;
        }
        removeNode(node);
    };

    const indentNode = (node: Node) => {
        const parentNode = getParentNode(node);
        const searchNodes = parentNode ? parentNode.nodes : nodes;
        const nodeIndex = searchNodes.findIndex(x => x === node);

        if (nodeIndex === 0) {
            return;
        } else {
            searchNodes.splice(nodeIndex, 1);
            searchNodes[nodeIndex - 1].nodes.push(node);
            setNodes([...nodes]);
        }
    };

    const unindentNode = (node: Node) => {
        const parentNode = getParentNode(node);

        if(parentNode === null) {
            return;
        }

        const parentParentNode = getParentNode(parentNode);
        const targetNodes = parentParentNode ? parentParentNode.nodes : nodes;
        const parentNodeIndex = targetNodes.findIndex(x => x === parentNode);
        targetNodes.splice(parentNodeIndex + 1, 0, node);
        const nodeIndex = parentNode.nodes.findIndex(x => x === node);
        parentNode.nodes.splice(nodeIndex, 1);

        setNodes([...nodes]);
    };

    const commands: ICommand[] = [
        { name: 'indent', bindKey: { win: 'tab', mac: 'tab' }, exec: () => {
            setTimeout(() => {
                indentNode(active);
            }, 0);
            return true;
        }},
        { name: 'unindent', bindKey: { win: 'shift+tab', mac: 'shift+tab' }, exec: () => {
            setTimeout(() => {
                unindentNode(active);
            }, 0);
            return true;
        }},
        { name: 'enter', bindKey: { win: 'enter', mac: 'enter' }, exec: () => {
            if (active.text === '') {
                return;
            }
            const above = inputRef.current && inputRef.current.editor.getCursorPosition().row === 0 && inputRef.current.editor.getCursorPosition().column === 0;
            setTimeout(() => {
                const newNode = insertNode(active, !!above);
                setActive(newNode);
            }, 0);
            return true;
        }},
        { name: 'up', bindKey: { win: 'up', mac: 'up' }, exec: () => {
            const onFirstRow = inputRef.current && inputRef.current.editor.getCursorPosition().row === 0;
            if (!onFirstRow) {
                return false;
            }
            const searchNodes = preorderTraversal();
            let activeIndex = searchNodes.findIndex(x => x === active);
            activeIndex = Math.max(0, activeIndex - 1);
            setTimeout(() => {
                setActive(searchNodes[activeIndex]);
                removeNodeIfEmpty(active);
            }, 0);
            return true;
        }},
        { name: 'down', bindKey: { win: 'down', mac: 'down' }, exec: () => {
            const onLastRow = inputRef.current && inputRef.current.editor.getCursorPosition().row + 1 === inputRef.current.editor.getSession().doc.getLength();
            if(!onLastRow) {
                return false;
            }
            const searchNodes = preorderTraversal();
            let activeIndex = searchNodes.findIndex(x => x === active);
            activeIndex = Math.min(searchNodes.length - 1, activeIndex + 1);
            setTimeout(() => {
                setActive(searchNodes[activeIndex]);
                removeNodeIfEmpty(active);
            }, 0);
            return true;
        }},
        { name: 'space', bindKey: { win: 'space', mac: 'space' }, exec: () => {
            const atStart = inputRef.current && inputRef.current.editor.getCursorPosition().row === 0 && inputRef.current.editor.getCursorPosition().column === 0;
            if (atStart) {
                setTimeout(() => {
                    indentNode(active);
                }, 0);
                return true;
            }
            return false;
        }},
        { name: 'backspace', bindKey: { win: 'backspace', mac: 'backspace' }, exec: () => {
            const atStart = inputRef.current && inputRef.current.editor.getCursorPosition().row === 0 && inputRef.current.editor.getCursorPosition().column === 0;
            if (atStart) {
                setTimeout(() => {
                    unindentNode(active);
                }, 0);
                return true;
            }
            return false;
        }}
    ];

    // Look at https://github.com/ajaxorg/ace/blob/41ac47ec9e26f65fe0ffedeedf14b525a22f5462/lib/ace/ext/language_tools.js#L42-L48
    const completions = {
        getCompletions: (editor: any, session: any, pos: any, editorPrefix: string, callback: CompletionCallback) => {
            const line: string = session.getLine(pos.row);

            if (!line) {
                return;
            }

            function getPrefix(position: any) {
                let prefix = '';

                for(let i=position.column - 1;i>=0;i--) {
                    if (line[i] === ' ') {
                        break;
                    }
                    
                    prefix = line[i] + prefix;
                }

                return prefix;    
            }

            function hasPrefix(requiredPrefix: string) {
                return getPrefix(pos).startsWith(requiredPrefix);
            }

            if (hasPrefix('[[')) {
                const completions = noteTitles.map(n => ({
                    caption: n.title,
                    snippet: n.title,
                    meta: n.context || 'note',
                }))

                callback(null, completions);
            } else if (hasPrefix('@')) {
                const completions = peopleAliases.map(n => ({
                    caption: n,
                    snippet: n,
                    meta: 'person',
                }))

                callback(null, completions);
            } else if (hasPrefix('::')) {
                // To use for properties
                //
                // const completions = peopleAliases.map(n => ({
                //     caption: n+'::',
                //     snippet: n+'::',
                //     meta: 'person',
                //     completer: {
                //         insertMatch: function (insertEditor: any, data: any) {
                //             var insertValue = data.snippet;
                //             var position = editor.selection.getCursor();

                //             insertEditor.session.replace({
                //                 start: { row: position.row, column: position.column - getPrefix(position).length },
                //                 end: { row: position.row, column: position.column }
                //             }, insertValue);
                //         }
                //     }
                // }))

                // callback(null, completions);
            }
        }
    }

    function toggleSelected(node: Node) {
        if(selectedNodes.includes(node)) {
            setSelectedNodes(selectedNodes.filter(n => n !== node));
        } else {
            setSelectedNodes(selectedNodes.concat(node))
        }
    }

    function clickNode(node: Node) {
        if (selectMode) {
            toggleSelected(node);
            return;
        }
        
        if(node !== active) {
            setActive(node);
        }
    }

    const renderNode = (node: Node, index: number, level: number, selectedNodes: Node[]) => {
        const isActive = node === active;
        const isSelected = selectedNodes.includes(node);
        return (
            <React.Fragment key={`${level}-${index}`}>
                <div style={{display: 'flex', background: isSelected ? '#003838' : !selectMode && isActive ? getVariable('--active-tree-editor-background') : undefined, padding: '.6rem .8rem'}} className={hoverGlow} onClick={() => clickNode(node)}>
                    {selectMode && <div style={{lineHeight: 1.5, marginRight: 8}}><input type="checkbox" checked={isSelected} onChange={() => toggleSelected(node)} /></div>}
                    {!selectMode && <div style={{letterSpacing: '.5rem', color: isActive ? '#078263' : '#777', lineHeight: 1.5}}>•</div>}
                    {!selectMode && isActive
                        ? <AceEditor
                            key="editor"
                            ref={inputRef}
                            value={node.text}
                            fontSize={16}
                            style={{background: 'transparent', width: '100%', lineHeight: 1.5, marginLeft: -4}}
                            onChange={e => onInputChanged(node, e)}
                            mode="markdown"
                            theme={isDark() ? "monokai" : 'textmate'}
                            focus
                            highlightActiveLine={false}
                            showGutter={false}
                            showPrintMargin={false}
                            wrapEnabled={true}
                            maxLines={Infinity}
                            commands={commands}
                            enableBasicAutocompletion={[completions, snippetCompleter, textCompleter, keyWordCompleter] as any}
                            enableLiveAutocompletion={true}
                            enableSnippets={true}
                            />
                        : <MarkedViewer markdown={node.text} contextId={props.contextId} />}
                </div>
                <div style={{borderLeft: '1px solid #555', marginLeft: 16, paddingLeft: 16}}>
                    {node.nodes.map((n, i) => renderNode(n, i, level + 1, selectedNodes))}
                </div>
            </React.Fragment>
        );
    };

    // function onMouseDown(node: Node) {
    //     console.log('mouseDown', node);
    //     setMouseDown(node);
    // }

    // function onMouseUp(node: Node) {
    //     console.log('mouseUp', node);
    //     setMouseDown(null);
    //     setMouseOver(null);
    // }

    // function onMouseOver(node: Node) {
    //     if(mouseDown) {
    //         console.log('mouseOver', node);
    //         setMouseOver(node);

    //         const newSelection: Node[] = [];

    //         if (mouseDown && mouseOver && mouseDown !== mouseOver) {
    //             let count = 0;
        
    //             const stack: Node[] = [];
        
    //             for(let i=nodes.length-1;i>=0;i--) {
    //                 stack.push(nodes[i]);
    //             }
        
    //             while (stack.length) {
    //                 const node = stack.pop();
        
    //                 if (!node) {
    //                     continue;
    //                 }
        
    //                 for(let i=node.nodes.length-1;i>=0;i--) {
    //                     stack.push(node.nodes[i]);
    //                 }
        
    //                 if (node === mouseDown || node === mouseOver) {
    //                     count += 1;
    //                     newSelection.push(node);
    //                 } else if (count === 1) {
    //                     newSelection.push(node);
    //                 }
    //             }

    //             setSelectedNodes(newSelection);
    //         }
    //     }
    // }

    function toggleSelectMode() {
        if(selectMode) {
            setSelectMode(false);
            setSelectedNodes([]);
        } else {
            setSelectMode(true);
        }
    }

    function copySelected() {
        let result = "";

        visitNodes((node, depth, index) => {
            if(!selectedNodes.includes(node)) {
                return;
            }

            console.log(node, depth);

            result += `${'  '.repeat(depth)}- ${node.text}\n`;
        });

        copy(result);
    }

    return (
        <>
            <div style={{display: 'flex', background: '#444', position: 'fixed', height: 36, bottom: 0, left: 0, right: 0, zIndex: 100}} className={cx(horizontalSpace('.5rem'))}>
                    <div className="button" onClick={toggleSelectMode}>Select</div>
                    {selectMode ? <>
                        <div className="button" onClick={() => setSelectedNodes(preorderTraversal())}>All</div>
                        <div className="button" onClick={copySelected}>Copy</div>
                    </> : <>
                        <div className="button" onClick={e => { unindentNode(active); inputRef.current && inputRef.current.editor.focus(); }}>&larr;</div>
                        <div className="button" onClick={e => { indentNode(active); inputRef.current && inputRef.current.editor.focus(); }}>&rarr;</div>
                        <div className="button" onClick={e => {
                            if (!inputRef.current) {
                                return;
                            }
                            inputRef.current.editor.session.insert(inputRef.current.editor.getCursorPosition(), '[[');
                            inputRef.current.editor.focus();
                        }}>[[</div>
                        <div className="button" onClick={e => {
                            if (!inputRef.current) {
                                return;
                            }
                            inputRef.current.editor.session.insert(inputRef.current.editor.getCursorPosition(), ']]');
                            inputRef.current.editor.focus();
                        }}>]]</div>
                    </>}

            </div>
            <div style={{height: '100%', marginBottom: 36}}>
                {nodes.map((n, i) => renderNode(n, i, 0, selectedNodes))}
            </div>
        </>
    );
};

interface Completion {
    value?: string;
    meta: string;
    type?: string;
    caption?: string;
    snippet?: any;
    score?: number;
    exactMatch?: number;
    docHTML?: string;
}

type CompletionCallback = (error: Error|null, results: Completion[]) => void;