import * as React from 'react';

export function TagInput(props: { tags: string; availableTags: string[]; onChange: (tags: string) => void }) {
    const [focus, setFocus] = React.useState(false)

    const [tagState, setTagState] = React.useState(new TagState(props.availableTags, props.tags && props.tags.length > 0 ? props.tags.split(' ') : []))

    const timeoutId = React.useRef<number|undefined>(undefined);
    const tagInput = React.useRef<any>();

    function onTextChange(e: React.ChangeEvent<HTMLInputElement>) {
        onChange(tagState.withText(e.target.value))
    }

    function onChange(newTagState: TagState) {
        setTagState(newTagState);
        props.onChange(newTagState.getTags());
    }

    function onKeyPress(e: React.KeyboardEvent<HTMLInputElement>) {
        if (e.key === 'ArrowUp') {
            onChange(tagState.decrementIndex());
        } else if (e.key === 'ArrowDown') {
            onChange(tagState.incrementIndex());
        } else if (e.key === 'Enter') {
            if (tagState.text === '') {
                onChange(tagState.addSelectedTag());
            } else {
                onChange(tagState.addText());
            }
        } else if (e.key === 'Tab') {
            onChange(tagState.addSelectedTag());
            e.preventDefault();
        } else if (e.key === 'Backspace') {
            if (tagState.text === '') {
                onChange(tagState.reopenLastTag());
            }
        }
    }

    function onClickTag(tag: string) {
        onChange(tagState.addTag(tag));
        tagInput.current.focus();
    }

    function onFocus() {
        clearTimeout(timeoutId.current);

        if(focus) {
            return;
        }

        setFocus(true);
    }

    function onBlur() {
        if(!focus) {
            return;
        }

        timeoutId.current = setTimeout(() => {
            setFocus(false);
        }, 100) as any;
    }

    return (
        <div className="tag-input" onFocus={() => onFocus()} onBlur={() => onBlur()}>
            <div className="tags">
                {tagState.selectedTags.map((t, i) => <Tag key={`tag-${t}-${i}`} name={t} />)}
                <input ref={tagInput} placeholder="Tag" type="text" className="" onChange={e => onTextChange(e)} onKeyDown={e => onKeyPress(e)} value={tagState.text} />
            </div>
            {focus && tagState.getFilteredAvailableTags().length > 0 &&
                <div className="panel">
                    {tagState.getFilteredAvailableTags().slice(0, 10).map((t, i) => <div key={`available-tag-${t}`} style={tagState.index === i ? { background: '#078263'} : {}} onClick={() => onClickTag(t)}>{t}</div>)}
                </div>
            }
        </div>
    );
}

export class TagState {
    availableTags: string[];
    selectedTags: string[];
    text: string;
    index: number;

    constructor(availableTags: string[], selectedTags: string[], text = "", index = 0) {
        this.availableTags = availableTags;
        this.selectedTags = selectedTags;
        this.text = text;
        this.index = index;
    }

    withText(text: string) {
        return new TagState(this.availableTags, this.selectedTags, text, this.index);
    }

    withAvailableTags(availableTags: string[]) {
        return new TagState(availableTags, this.selectedTags, this.text, this.index);
    }

    withSelectedTags(selectedTags: string[]) {
        return new TagState(this.availableTags, selectedTags, this.text, this.index);
    }

    decrementIndex() {
        return new TagState(this.availableTags, this.selectedTags, this.text, Math.max(0, this.index - 1));
    }

    incrementIndex() {
        return new TagState(this.availableTags, this.selectedTags, this.text, Math.max(0, Math.min(this.availableTags.length - 1, this.index + 1)));
    }

    addSelectedTag() {
        return new TagState(this.availableTags, this.selectedTags.concat([this.getFilteredAvailableTags()[this.index]]), '', 0);
    }

    addTag(tag: string) {
        return new TagState(this.availableTags, this.selectedTags.concat([tag]), '', 0);
    }

    addText() {
        return new TagState(this.availableTags, this.selectedTags.concat([this.text]), '', 0);
    }

    reopenLastTag() {
        const lastTag = this.selectedTags[this.selectedTags.length - 1];
        return new TagState(this.availableTags, this.selectedTags.slice(0, this.selectedTags.length - 1), lastTag, 0);
    }

    getFilteredAvailableTags() {
        return this.availableTags.filter(t => this.selectedTags.indexOf(t) === -1).filter(t => t.toLowerCase().indexOf(this.text) !== -1);
    }

    getTags() {
        return this.selectedTags.join(' ');
    }
}

export const Tag: React.FC<{ name: string }> = props => {
    return (
        <div className="tag is-dark">{props.name}</div>
    );
}

export const TagList: React.FC<{tags: string}> = props => {
    return (
        <div className="tags" style={{marginBottom: 0}}>{props.tags && props.tags.split(' ').map((t, i) => <Tag key={i} name={t} />)}</div>
    );
}