import * as React from 'react';
import { useData } from '../model';
import { ContextState, Context } from '../db/context';
import * as styles from '../layout/styles';
import * as ContextActions from '../lib/contextActions';
import { cx } from 'emotion';
import { getVariable } from '../layout/theme';

export const ContextBar: React.FC<{contextId: string|null, onChange: (contextId: string|null) => void, showTaskCount?: boolean}> = (props) => {
    const {data} = useData();

    const nestedContexts = ContextActions.getNestedContexts(data);

    const renderListContext = (context: Context, contexts: ContextActions.NestedContexts, depth = 0) => {
        if (context.state && context.state === ContextState.Inactive) {
            return <React.Fragment key={context.id}></React.Fragment>;
        }

        return (
            <React.Fragment key={context.id}>
                <div onClick={() => props.onChange(context.id)} style={{paddingLeft: 10 + depth * 10, background: props.contextId === context.id ? getVariable('--active-context-background') : undefined}} className={cx('item', styles.hoverGlow, styles.cursorPointer)}>
                    {context.title} {props.showTaskCount && <span style={{color: '#999'}}>{contexts.taskCounts[context.id]}</span>}
                </div>
                {contexts.nestedContexts[context.id] && contexts.nestedContexts[context.id].map(c => (
                    renderListContext(c, contexts, depth + 1)
                ))}
            </React.Fragment>
        );
    }

    return (
        <div style={{borderRight: '1px solid #666', width: 200}}>
            <div className="list">
                <div className={cx('item', styles.hoverGlow, styles.cursorPointer)} style={{paddingLeft: 10, background: props.contextId === null ? getVariable('--active-context-background') : undefined}} onClick={() => props.onChange(null)}>All</div>
                {nestedContexts.topContexts.map(i => renderListContext(i, nestedContexts))}
            </div>
        </div>
    )
}