import * as React from 'react';
import { useData } from '../model';
import { MarkedViewer } from './MarkedViewer';
import { getNoteTitle, getNoteReferenceMatchRegExp, getNoteAliasReferenceMatchRegExp, NoteMode } from '../db/note';
import { Link } from 'react-router-dom';
import * as _ from 'lodash';
import { TreeViewer } from './TreeViewer';

export const ReferenceViewer: React.FC<{ title: string, contextId: string|null }> = (props) => {
    const {data} = useData();
    const referenceRegExp = getNoteReferenceMatchRegExp(props.title);
    const contextNotes = _.orderBy(data.notes.filter(x => x.contextId === props.contextId && referenceRegExp.test(x.content)), x => x.updatedAt, 'desc');

    if (contextNotes.length === 0) {
        return null;
    }

    return (
        <div style={{paddingTop: 32}}>
            <div style={{fontWeight: 'bold'}}>{contextNotes.length} linked references for "{props.title}"</div>
            {contextNotes.map(note => (
                <div key={note.id} style={{marginTop: 16}}>
                    <Link to={`/notes/${note.id}`} style={{color: '#078263'}}>{getNoteTitle(note)}</Link>
                    <div style={{paddingLeft: 16, background: '#222', marginTop: 8}}>
                        {note.mode === NoteMode.Markdown
                        ? <MarkedViewer markdown={note.content} contextId={note.contextId} /> 
                        : <TreeViewer json={note.content} contextId={note.contextId} referenceTitle={props.title} />}
                    </div>
                </div>
            ))}
        </div>
    );
};

export const PersonReferenceViewer: React.FC<{ alias: string }> = (props) => {
    const {data} = useData();
    const referenceRegExp = getNoteAliasReferenceMatchRegExp(props.alias);
    const matchingNotes = _.orderBy(data.notes.filter(x => referenceRegExp.test(x.content)), x => x.updatedAt, 'desc');

    if (matchingNotes.length === 0) {
        return null;
    }

    return (
        <div style={{paddingTop: 32}}>
            <div style={{fontWeight: 'bold'}}>{matchingNotes.length} linked references for "{props.alias}"</div>
            {matchingNotes.map(note => (
                <div key={note.id} style={{marginTop: 16}}>
                    <Link to={`/notes/${note.id}`} style={{color: '#078263'}}>{getNoteTitle(note)}</Link>
                    <div style={{paddingLeft: 16, background: '#222', marginTop: 8}}>
                        {note.mode === NoteMode.Markdown 
                        ? <MarkedViewer markdown={note.content} contextId={note.contextId} /> 
                        : <TreeViewer json={note.content} contextId={note.contextId} personAlias={props.alias} />}
                    </div>
                </div>
            ))}
        </div>
    );
};