import { Entity } from "./entity";
import { HasNote } from "./note";

export interface Person extends Entity, HasNote {
    type: 'person';
    name: string;
    alias: string|null;
    birthday: string|null;
}

export const mentionRegex = /\B(@[^\s()[\].,!:]+)/g;

export function convertNameToAlias(name: string) {
    if(!name) {
        return null;
    }

    return name.toLowerCase().replace(/ /g, '_');
}

export function getAlias(person: Person) {
    return `${person.alias || convertNameToAlias(person.name)}`;
}