import * as React from 'react';
import * as styles from '../layout/styles';
import { cx } from 'emotion';

export const ToolSection: React.SFC<{ verticalAround?: boolean }> = (props) => {
    let verticalAround = props.verticalAround;

    if (verticalAround === undefined) {
        verticalAround = true;
    }

    return (
        <div className={cx(styles.horizontalSpace('8px', true), styles.verticalSpace('8px', verticalAround))}>{props.children}</div>
    );
}