import { Data } from "../model";
import * as _ from 'lodash';
import { Context } from "../db/context";
import { TaskState } from "../db/task";

export interface NestedContexts {
    topContexts: Context[];
    nestedContexts: {[id: string]: Context[]};
    taskCounts: {[id: string]: number};
}

export function getNestedContexts(data: Data): NestedContexts {
    const grouped = _.groupBy(data.contexts, x => x.contextId);
    const taskCounts = _.countBy(data.tasks.filter(x => x.state === TaskState.Active), x => x.contextId);

    return {
        topContexts: data.contexts.filter(x => !x.contextId),
        nestedContexts: grouped,
        taskCounts
    };
}

export function getFilteredContextIds(contextId: string|null, data: Data) {
    const contexts = getNestedContexts(data);

    if (contextId) {
        const contextIds = [contextId];
        const queue = [contextId];

        while (queue.length) {
            const next = queue.pop();

            if (!next) {
                break;
            }

            for(const nested of contexts.nestedContexts[next] || []) {
                contextIds.push(nested.id);
                queue.push(nested.id);
            }
        }

        return contextIds;
    } else {
        return data.contexts.map(c => c.id);
    }
};