export class EventListener<T> {
    listeners: ((arg: T) => void)[] = [];

    register(listener: (arg: T) => void) {
        this.listeners.push(listener);
        return () =>  {
            this.listeners = this.listeners.filter(x => x !== listener);
        };
    }

    trigger(arg: T) {
        for(const listener of this.listeners) {
            listener(arg);
        }
    }
}