import * as React from 'react';
import { Data } from '../model';
import { ContextState } from '../db/context';
import _ from 'lodash';

export const ContextSelector: React.FC<{contextId: string|null, data: Data, onChange: (contextId: string|null) => void}> = (props) => {
    const activeContexts = _.orderBy(props.data.contexts.filter(x => x.state === ContextState.Active), x => x.title);
    const inactiveContexts = _.orderBy(props.data.contexts.filter(x => x.state !== ContextState.Active), x => x.title);

    return (
        <select className="input is-large" value={props.contextId || ''} onChange={e => props.onChange(e.target.value)}>
            <option value="">Not set</option>
            {activeContexts.map(c => (<option key={c.id} value={c.id}>{c.title}</option>))}
            <option value="--inactive--" disabled>-- Inactive</option>
            {inactiveContexts.map(c => (<option key={c.id} value={c.id}>{c.title}</option>))}
        </select>
    );
}