import React from "react";
import { Context } from "../db/context";
import { Note } from "../db/note";
import { Person } from "../db/person";
import { Project } from "../db/project";
import { Task } from "../db/task";

function getBridge() {
    const desktop: HostBridge = (window as any).chrome?.webview?.hostObjects?.dotnet;
    const android: HostBridge = (window as any).AndroidBridge;
    return desktop || android || null;
}

function hasBridge() {
    return !!getBridge();
}

interface HostBridge {
    getNotes(): Promise<string>
    getNote(id: string): Promise<string>;
    saveNote(entityJson: string): Promise<string>;
    getContexts(): Promise<string>;
    getContext(id: string): Promise<string>;
    saveContext(entityJson: string): Promise<string>;
    getPeople(): Promise<string>;
    savePerson(entityJson: string): Promise<string>;
    getProjects(): Promise<string>;
    saveProject(entityJson: string): Promise<string>;
    getTasks(): Promise<string>;
    saveTask(entityJson: string): Promise<string>;
    getEntity(key: string): Promise<string>;
    sendEntity(key: string, contents: string): Promise<void>;
    syncRemote(): Promise<void>
    syncRemoteConnected(): Promise<boolean>
    disconnectBlobStorage(): Promise<void>
    connectBlobStorage(connectionString: String): Promise<void>
}

async function getContext(id: string) {
    return JSON.parse(await getBridge().getContext(id)) as Context;
}

async function getContexts() {
    return JSON.parse(await getBridge().getContexts()) as Context[];
}

async function saveContext(entity: Context) {
    return JSON.parse(await getBridge().saveContext(JSON.stringify(entity))) as Context;
}

async function getPeople() {
    return JSON.parse(await getBridge().getPeople()) as Person[];
}

async function savePerson(entity: Person) {
    return JSON.parse(await getBridge().savePerson(JSON.stringify(entity))) as Person;
}

async function getProjects() {
    return JSON.parse(await getBridge().getProjects()) as Project[];
}

async function saveProject(entity: Project) {
    return JSON.parse(await getBridge().saveProject(JSON.stringify(entity))) as Project;
}

async function getTasks() {
    return JSON.parse(await getBridge().getTasks()) as Task[];
}

async function saveTask(entity: Task) {
    return JSON.parse(await getBridge().saveTask(JSON.stringify(entity))) as Task;
}

async function getNotes() {
    return JSON.parse(await getBridge().getNotes()) as Note[];
}

async function getNote(id: string) {
    return JSON.parse(await getBridge().getNote(id)) as Note;
}

async function saveNote(entity: Note) {
    return JSON.parse(await getBridge().saveNote(JSON.stringify(entity))) as Note;
}

async function syncRemote() {
    await getBridge().syncRemote();
}

async function syncRemoteConnected() {
    return await getBridge().syncRemoteConnected();
}

async function disconnectBlobStorage() {
    await getBridge().disconnectBlobStorage();
}

async function connectBlobStorage(connectionString: string) {
    await getBridge().connectBlobStorage(connectionString);
}

export const useNotes = () => {
    const [data, setData] = React.useState<Note[]|null>(null);
  
    async function fetchData() {
        console.time('getNotes');
        const response = await Api.getNotes();
        console.timeEnd('getNotes');
        setData(response);
    }
  
    React.useEffect(() => {fetchData()}, []);
    return data;
};

export const useNote = (id: string|null) => {
    const [data, setData] = React.useState<Note|null>(null);
  
    async function fetchData(id: string|null) {
        if(!id) {
            return;
        }

        console.time('getNote');
        const response = await Api.getNote(id);
        console.timeEnd('getNote');
        setData(response);
    }
  
    React.useEffect(() => {fetchData(id)}, [id]);
    return data;
};

export const useContext = (id: string|null) => {
    const [data, setData] = React.useState<Context|null>(null);
  
    async function fetchData(id: string|null) {
        if(!id) {
            return;
        }

        const response = await Api.getContext(id);
        setData(response);
    }
  
    React.useEffect(() => {fetchData(id)}, [id]);
    return data;
};

export const Api = {
    hasBridge,
    getNotes,
    getNote,
    saveNote,
    getContexts,
    getContext,
    saveContext,
    getProjects,
    saveProject,
    getPeople,
    savePerson,
    getTasks,
    saveTask,
    syncRemote,
    syncRemoteConnected,
    connectBlobStorage,
    disconnectBlobStorage,
};

