import { Note } from "./db/note";
import { Task } from "./db/task";
import { Context } from "./db/context";
import { Project } from "./db/project";
import { Person } from "./db/person";
import React, { useContext, useEffect } from "react";
import { syncer } from "./db/syncer";
import { Loader } from "./components/Loader";

export class Data {
    constructor(notes: Note[], tasks: Task[], contexts: Context[], projects: Project[], people: Person[]) {
        this.notes = notes;
        this.tasks = tasks;
        this.contexts = contexts;
        this.projects = projects;
        this.people = people;
    }

    notes: Note[];
    readonly tasks: Task[];
    readonly contexts: Context[];
    readonly projects: Project[];
    readonly people: Person[];
};

interface DataContextState {
    data: Data;
    refresh(): void;
}

const DataContext = React.createContext<DataContextState>({
    data: null as any,
    refresh() {},
});

export const DataProvider: React.FC = props => {
    const [data, setData] = React.useState<Data>(null as any);

    useEffect(() => {
        async function init() {
            syncer.registerDataListener(async data => setData(data));
            setData(await syncer.getData());
            await syncer.startServerSync();
            await syncer.ensureServerSyncEnqueued();
        };
        init();
    }, []);

    const value: DataContextState = {
        data,
        refresh() {
            syncer.getData(true);
        }
    };

    if (!data) {
        return <Loader />;
    }

    return (
      <DataContext.Provider value={value}>
        {props.children}
      </DataContext.Provider>
    );
};

export const useData = () => {
    const value = useContext(DataContext);
    return value;
};