import React from 'react';
import { ToolSection } from '../framework/ToolSection';
import { syncer, SyncStatus } from '../db/syncer';
import moment from 'moment';

export const SyncPage: React.FC<{}> = props => {
    const [count, setCount] = React.useState(0);

    React.useEffect(() => {
        syncer.registerSyncListener(() => {
            setCount(c => c + 1);
        });
    }, []);
   
    return (
        <>
            <React.Fragment key={count}></React.Fragment>
            <ToolSection>
                <div>Status: {SyncStatus[syncer.status]}</div>
                <div className="button" onClick={() => syncer.startServerSync()}>Sync Now</div>
            </ToolSection>
            <div className="list">
                {syncer.getResults().slice(0).reverse().map((result, i) =>
                    <React.Fragment key={i}>
                        <div className="item">{result.type} - {result.endAt.getTime() - result.startAt.getTime()} ms - start: {moment(result.startAt).format('HH:mm:ss')}, end: {moment(result.endAt).format('HH:mm:ss')}, fetch: {result.fetch}, send: {result.send}{result.error ? `, error: ${result.error}` : null}</div>
                    </React.Fragment>)}
            </div>
        </>
    );
}
