import {css} from 'emotion';

export const hoverGlow = 'hover-glow';

export const cursorPointer = css({
    cursor: 'pointer'
});

export const verticalCenter = css({
    display: 'flex',
    alignItems: 'center'
});

export function horizontalSpace(width = '.3rem', around = false) {
    return css({
        paddingLeft: around ? width : undefined,
        paddingRight: around ? width : undefined,
        '&': {
            '& > *:first-child': {
                marginLeft: 0
            },
            '& > *': {
                marginRight: width
            },
            '& > *:last-child': {
                marginRight: 0
            }
        }
    });
}

export function verticalSpace(height = '.3rem', around = false) {
    return css({
        paddingTop: around ? height : undefined,
        paddingBottom: around ? height : undefined,
        '&': {
            '& > *': {
                marginBottom: height
            },
            '& > *:last-child': {
                marginBottom: 0
            }
        }
    });
}