import * as React from "react";

export const useQueryState = (initialValue: string|null, queryParam: string): [string|null, (value: string|null) => void] => {
    const [rawValue, setRawValue] = React.useState(() => {
        const urlParams = new URLSearchParams(window.location.search);
        return urlParams.get(queryParam) || initialValue;
    });

    const setValue = (value: string|null) => {
        const urlParams = new URLSearchParams(window.location.search);
        setRawValue(value);
        if (!value || value === initialValue) {
            urlParams.delete(queryParam);
        } else {
            urlParams.set(queryParam, value);
        }

        const queryString = urlParams.toString();

        if (queryString.length) {
            window.history.replaceState(null, '', `?${queryString}`);
        } else {
            window.history.replaceState(null, '', window.location.pathname);
        }

        window.dispatchEvent(new (window as any).CustomEvent('popstate'));
    };

    return [rawValue, setValue];
};

export const useQueryStateT = <T extends {}>(initialValue: T, queryParam: string, convertTo: (value: string|null) => T, convertFrom: (value: T) => string|null): [T, (value: T) => void] => {
    const [rawValue, setRawValue] = React.useState<T>(() => {
        const urlParams = new URLSearchParams(window.location.search);
        return convertTo(urlParams.get(queryParam)) || initialValue;
    });

    const setValue = (value: T) => {
        const urlParams = new URLSearchParams(window.location.search);
        setRawValue(value);
        if (!value || value === initialValue) {
            urlParams.delete(queryParam);
        } else {
            const strValue = convertFrom(value);
            if (strValue) {
                urlParams.set(queryParam, strValue);
            } else {
                urlParams.delete(queryParam);
            }
        }

        const queryString = urlParams.toString();

        if (queryString.length) {
            window.history.replaceState(null, '', `?${queryString}`);
        } else {
            window.history.replaceState(null, '', window.location.pathname);
        }

        window.dispatchEvent(new (window as any).CustomEvent('popstate'));
    };

    return [rawValue, setValue];
};

export const useQueryStateBoolean = (initialValue: boolean, queryParam: string) => {
    return useQueryStateT<boolean>(initialValue, queryParam, x => x === 'true', x => x === initialValue ? null : x.toString());
};