import * as React from 'react';
import { NavLink, Redirect } from 'react-router-dom';
import { NoteEditor } from '../../components/NoteEditor';
import { useData } from '../../model';
import { ToolSection } from '../../framework/ToolSection';
import { useQueryState } from '../../lib/UseQueryState';
import * as styles from '../../layout/styles';
import { cx } from 'emotion';
import { Note, NoteMode } from '../../db/note';
import { register } from '../../db/stagingArea';
import moment from 'moment';

function useNote(pristineNote: Note): [Note, (props: Partial<Note>) => void, boolean] {
    const [pristine] = React.useState(pristineNote);
    const [note, setNote] = React.useState<Note>({...pristineNote});
    const [hasChanges, setHasChanges] = React.useState(false);

    function updateNote(props: Partial<Note>) {
        const newNote = {...note, ...props};
        const newHasChanges = JSON.stringify(pristine) !== JSON.stringify(newNote);
        setHasChanges(newHasChanges);
        setNote(newNote);
        register(newNote, newHasChanges);
    }

    return [note, updateNote, hasChanges];
}

export const EditNotePage: React.FC<{id?: string }> = props => {
    const {data} = useData();
    let tags: {[key: string]: number} = {};
    const [goBack, setGoBack] = React.useState(false);
    const [source] = useQueryState(null, 'source');

    const urlParams = new URLSearchParams(window.location.search);
    const title = urlParams.get('title');
    const contextId = urlParams.get('context');
    const daily = urlParams.has('daily');

    const [note, updateNote] = useNote(data.notes.find(x => x.id === props.id) || { title: title || '', content: '', mode: NoteMode.Tree, contextId, dailyDate: (daily && moment().format('YYYY-MM-DD')) || null, noteFor: null, tags: '' } as Note);

    if (goBack) {
        return <Redirect to={source ? decodeURIComponent(source) : '/'} />;
    }

    for (const note of data.notes || []) {
        if(!note.tags || !note.tags.length) {
            continue;
        }

        for(const tag of note.tags.split(' ')) {
            if(!tags[tag]) {
                tags[tag] = 0;
            }

            tags[tag] += 1;
        }
    }

    const availableTags = Object.keys(tags).sort((a, b) => tags[b] - tags[a]);

    if (props.id) {
        return (
            <>
                <div style={{height: '100%', display: 'flex', flexDirection: 'column'}}>
                    <ToolSection>
                        <div className={cx(styles.horizontalSpace('.5rem'))}>
                            <NavLink to={source ? decodeURIComponent(source) : '/'} className="button">Back</NavLink>
                            {!note.noteFor && <NavLink to={`/notes/${note.id}`} className="button">Show</NavLink>}
                        </div>
                    </ToolSection>
                    <NoteEditor note={note} updateNote={updateNote} {...note} data={data} availableTags={availableTags} onNoteAdded={() => { setGoBack(true); }} />
                </div>
            </>
        );
    }

    return (
        <>
            <div style={{height: '100%', display: 'flex', flexDirection: 'column'}} >
                <ToolSection>
                    <NavLink to={`/`} className="button">Back</NavLink>
                </ToolSection>
                <NoteEditor note={note} updateNote={updateNote} availableTags={availableTags} data={data} onNoteAdded={() => { setGoBack(true); }} />
            </div>
        </>
    );
}