import * as React from 'react';
import marked from 'marked';
import '../github-markdown.css';
import { useData } from '../model';
import { getNoteTitleMatchRegExp, getNoteContextTitlePrefixRegExp, noteReferenceRegex } from '../db/note';
import {useHistory} from 'react-router-dom';
import { mentionRegex, getAlias } from '../db/person';


export const MarkedViewer: React.FC<{ markdown: string; contextId: string|null }> = props => {
    const history = useHistory();
    const {data} = useData();

    (window as any).lifeNavigate = function(url: string) {
        history.push(url);
        return false;
    };

    let markdown = props.markdown.replace(noteReferenceRegex, (_, group1) => {
        let matchContextId = props.contextId;
        let matchTitle = group1;

        const prefixContext = data.contexts.find(x => getNoteContextTitlePrefixRegExp(x.title).test(group1));
        if (prefixContext) {
            matchTitle = matchTitle.substring(prefixContext.title.length + 1);
            matchContextId = prefixContext.id;
        }

        const referenceRegExp = getNoteTitleMatchRegExp(matchTitle);
        const note = data.notes.find(x => x.contextId === matchContextId && referenceRegExp.test(x.title));

        if (note) {
            const url = `/notes/${note.id}`;
            return `<span style="color: #444">[[ </span><a onclick="return lifeNavigate('${url}')" href="${url}">${group1}</a><span style="color: #444"> ]]</span>`;
        }

        const url = `/notes/virtual?title=${encodeURIComponent(group1)}${props.contextId ? `&context=${props.contextId}` : ''}&source=${encodeURIComponent(`${window.location.pathname}`)}`;
        return `<span style="color: #444">[[ </span><a onclick="return lifeNavigate('${url}')" href="${url}">${group1}</a><span style="color: #444"> ? ]]</span>`;
    });

    markdown = markdown.replace(mentionRegex, (_, group1) => {
        const person = data.people.find(x => `@${getAlias(x)}` === group1);

        if (person) {
            const url = `/people/${person.id}?source=${encodeURIComponent(`${window.location.pathname}`)}`;
            return `<a onclick="return lifeNavigate('${url}')" href="${url}" style="background: #333; padding: 0px 4px; border-radius: 4px">${person.name}</a>`;
        }

        const url = `/people/virtual?alias=${encodeURIComponent(group1)}&source=${encodeURIComponent(`${window.location.pathname}`)}`;
        return `<a onclick="return lifeNavigate('${url}')" href="${url}" style="background: #333; padding: 0px 4px; border-radius: 4px">${group1}</a><span style="color: #444"> ?</span>`;
    });

    const html = marked(markdown);
    return (
        <div className="marked-viewer note markdown-body" dangerouslySetInnerHTML={{ __html: html }}></div>
    );
};