import * as React from 'react';
import { NavLink } from 'react-router-dom';
import * as styles from '../layout/styles';
import { ToolSection } from '../framework/ToolSection';
import { Context, ContextState } from '../db/context';
import { useData } from '../model';
import { cx } from 'emotion';
import { getVariable } from '../layout/theme';
import { Api } from '../bridge/HostBridge';

export const ContextPage: React.FC<{ refreshData(): void }> = props => {
    const {data} = useData();

    const [text, setText] = React.useState('');

    async function onKeyPress(e: React.KeyboardEvent<HTMLInputElement>) {
        if (e.key === 'Enter') {
            await Api.saveContext({
                title: text,
                state: ContextState.Active,
                contextId: null
            } as Context);

            props.refreshData();
            setText('');
        }
    }

    function renderTarget(target: Context) {
        return (
            <NavLink key={target.id} to={`/contexts/${target.id}`} className={cx('item', styles.hoverGlow)}>
                <div>
                    {target.title}
                </div>
            </NavLink>
        );
    }

    return (
        <>
            <ToolSection>
                <input type="text" className="input is-large is-wide" placeholder="Add context..." value={text} onChange={e => setText(e.target.value)} onKeyPress={onKeyPress} />
            </ToolSection>

            <div className="list">
                <div className="header" style={{background: getVariable('--task-context-background')}}>Active</div>
                {data.contexts.filter(p => p.state === ContextState.Active).sort((a, b) =>  b.title! < a.title! ?  -1 : b.title! > a.title! ? 1 : 0).map(t => renderTarget(t))}
                <div className="header" style={{background: getVariable('--task-context-background')}}>Not Active</div>
                {data.contexts.filter(p => p.state === ContextState.Inactive).sort((a, b) =>  b.title! < a.title! ?  -1 : b.title! > a.title! ? 1 : 0).map(t => renderTarget(t))}
            </div>
        </>
    );
}
