import * as React from 'react';
import { NavLink } from 'react-router-dom';
import { Data } from '../model';
import { ToolSection } from '../framework/ToolSection';
import { Project, ProjectState } from '../db/project';
import { Loader } from '../components/Loader';
import { ContextSelector } from '../components/ContextSelector';
import { Api } from '../bridge/HostBridge';

export class ShowProjectPage extends React.Component<{ data: Data, refreshData: () => void, id: string }, { title: string }> {
    constructor(props: any) {
        super(props);

        this.state = {
            title: '',
        }
    }

    componentDidMount() {
        let target = this.props.data.projects.find(x => x.id === this.props.id);
        this.setState({title: target!.title});
    }

    async setContext(target: Project, contextId: string|null) {
        if (contextId === '') {
            contextId = null;
        }

        target.contextId = contextId;
        await Api.saveProject(target);
        this.props.refreshData();
    }

    async setProjectState(target: Project, event: React.ChangeEvent<HTMLSelectElement>) {
        let projectState: ProjectState = event.target.value as unknown as ProjectState;

        target.state = projectState;
        await Api.saveProject(target);
        this.props.refreshData();
    }

    async onKeyDown(e: React.KeyboardEvent<HTMLInputElement>) {
        if (e.key === 'Enter') {
            let target = this.props.data!.projects.find(x => x.id === this.props.id);

            if (!target) {
                return;
            }

            if (target.title !== this.state.title) {
                target.title = this.state.title;
                await Api.saveProject(target);
                this.props.refreshData();
            }
        }
    }

    render() {
        if (this.props.data === null) {
            return <Loader />;
        }

        let target = this.props.data.projects.find(x => x.id === this.props.id);

        if (!target) {
            return <div>Project not found.</div>
        }

        let isDirty = false;

        if (target.title !== this.state.title) {
            isDirty = true;
        }

        return (
            <>
                <ToolSection>
                    <NavLink to={`/projects`} className="button">Back</NavLink>
                </ToolSection>

                <ToolSection>
                    <div>Project</div>
                    <input type="text" className="input is-large is-wide" style={{border: isDirty ? '2px solid orange' : undefined}} value={this.state.title} onKeyDown={e => this.onKeyDown(e)} onChange={e => this.setState({ title: e.target.value })} />
                    
                    <div>Context</div>
                    <ContextSelector contextId={target.contextId} onChange={e => this.setContext(target!, e)} data={this.props.data} />
                    <div>State</div>
                    <select className="input is-large" value={target.state || ProjectState.Active} onChange={e => this.setProjectState(target!, e)}>
                        {Object.keys(ProjectState).map(c => (
                            <option key={c} value={c}>{ProjectState[c as any]}</option>
                        ))}
                    </select>

                </ToolSection>

            </>
        );
    }
}