import * as React from 'react';
import { useData } from '../model';

export const ContextTag: React.FC<{contextId: string|null}> = (props) => {
    const {data} = useData();

    if (!props.contextId) {
        return null;
    }

    const context = data.contexts.find(p => p.id === props.contextId);

    if (!context) {
        return null;
    }

    return <div className="tag is-context">{context.title}</div>;
}