import {history} from '../index';
import { Note } from './note';
import {createInstance} from 'localforage';
import { Api } from '../bridge/HostBridge';
import { syncer } from './syncer';

let registered = false;

function ensureRegister() {
    if (registered) {
        return;
    }

    history.listen(onNavigate);
    registered = true;
}

const store = createInstance({ name: `staging-note` });

async function processQueue() {
    for(const key of await store.keys()) {
        const note = await store.getItem<Note>(key);
        await Api.saveNote(note);
        syncer.getData(true);
        await store.removeItem(note.id);
    }
}

processQueue();

let queuedPath: string|null = null;
let queuedNote: Note|null = null;

async function onNavigate(location: { pathname: string }, action: string) {
    if (!queuedNote) {
        return;
    }

    if (queuedPath === location.pathname) {
        return;
    }

    await Api.saveNote(queuedNote);
    syncer.getData(true);
    await store.removeItem(queuedNote.id);
    queuedNote = null;
}

export async function register(note: Note, hasChanges: boolean) {
    ensureRegister();
    if(hasChanges) {
        queuedNote = note;
        queuedPath = window.location.pathname;
        await store.setItem(note.id, note);
    } else {
        queuedNote = null;
        queuedPath = null;
        await store.removeItem(note.id);
    }
};