import * as React from 'react';
import { ToolSection } from '../framework/ToolSection';
import { setTheme } from '../layout/theme';
import { Api } from '../bridge/HostBridge';

export const SettingsPage: React.FC<{}> = () => {
    const [remoteConnected, setRemoteConnected] = React.useState<boolean|null>(null);
    const [blobConnectionString, setBlobConnectionString] = React.useState('');

    React.useEffect(() => {
        async function check() {
            setRemoteConnected(await Api.syncRemoteConnected());
        }
        check();
    }, []);

    return (
        <>
            <ToolSection>
                <div className="button" onClick={() => setTheme('dark-theme')}>Dark theme</div>
                <div className="button" onClick={() => setTheme('light-theme')}>Light theme</div>
            </ToolSection>

            <ToolSection>
                <div className="button" onClick={() => window.location.reload()}>Reload</div>
                <div className="button" onClick={() => window.location.reload(true)}>Force Reload</div>
            </ToolSection>
            <ToolSection>
                {remoteConnected ? (
                    <div className="button" onClick={async _ => { Api.disconnectBlobStorage(); setRemoteConnected(await Api.syncRemoteConnected()); }}>Disconnect Blob Storage</div>
                ) : (
                    <>
                        <div className="button" onClick={async _ => { Api.connectBlobStorage(blobConnectionString); setRemoteConnected(await Api.syncRemoteConnected()); }}>Connect Blob Storage</div>
                        <input type="text" value={blobConnectionString} onChange={e => setBlobConnectionString(e.target.value)} />
                    </>
                )}
            </ToolSection>
        </>
    );
}