import { Entity } from "./entity";
import { HasNote } from "./note";

export enum TaskState {
    Active,
    Incubated,
    Completed,
};

export interface Task extends Entity, HasNote {
    type: 'task',
    title: string;
    content: string|null;
    state: TaskState;
    stateChangedAt: string;
    contextId: string|null;
    projectId: string|null;
    deferredUntil: string|null;
}