import * as React from 'react';
import moment from 'moment';
import { TagList } from '../../components/TagInput';
import { NavLink } from 'react-router-dom';
import lunr from 'lunr';
import { debounce } from 'throttle-debounce';
import { useData } from '../../model';
import * as styles from '../../layout/styles';
import { ToolSection } from '../../framework/ToolSection';
import { getNoteTitle, extractAllReferences } from '../../db/note';
import { List } from '../../framework/List';
import { isMobile } from '../../components/Layout';
import { useQueryState, useQueryStateT, useQueryStateBoolean } from '../../lib/UseQueryState';
import { SettingsCompactor } from '../../components/SettingsCompactor';
import { ContextTag } from '../../components/ContextTag';
import _ from 'lodash';
import { Loader } from '../../components/Loader';
import { cx } from 'emotion';
import { ContextState } from '../../db/context';

export function NotePage() {
    const {data} = useData();
    const [text, setText] = useQueryState(null, 'search');
    const [filterTags, setFilterTags] = useQueryStateT<string[]>([], 'tags', s => s !== null ? s.split('|') : [], a => a.join('|'));
    const [showTagsBar, setShowTagsBar] = React.useState(!isMobile());
    const [searchResult, setSearchResult] = React.useState<{ id: string, score: number }[]|null>(null);
    const [searchIndex, setSearchIndex] = React.useState<lunr.Index|null>(null);
    const [showNotesFor, setShowNotesFor] = React.useState(false);
    const [showNoContext, setShowNoContext] = useQueryStateBoolean(false, 'no-context');
    const [virtualReferences, setVirtualReferences] = React.useState<{ title: string, contextId: string|null, updatedAt: Date }[]>();
    const [inactiveContexts, setInactiveContexts] = React.useState<string[]>([]);

    React.useEffect(() => {
        const refs = extractAllReferences(data.notes);
        setVirtualReferences(refs);
    }, [data.notes]);

    React.useEffect(() => {
        setInactiveContexts(data.contexts.filter(c => c.state === ContextState.Inactive).map(c => c.id));
    }, [data.contexts]);

    const onSearch = (text: string) => {
        if (!text || !text.length) {
            setSearchResult(null);
            return;
        }

        if (searchIndex === null) {
            const idx = lunr(function() {
                this.field('title');
                this.field('text');
    
                for(let note of data.notes) {
                    this.add({
                        id: note.id,
                        title: note.title,
                        text: note.content
                    });
                }
            });

            setSearchIndex(idx);
            const result = idx.search(text);
            setSearchResult(result.map(r => { return { id: r.ref, score: r.score }; }));
            return;
        }

        const result = searchIndex.search(text);
        setSearchResult(result.map(r => { return { id: r.ref, score: r.score }; }));
    };

    const debouncedSearch = debounce(100, onSearch);

    const toggleTag = (tag: string) => {
        if (filterTags.indexOf(tag) === -1) {
            setFilterTags(filterTags.concat([ tag ]));
        } else {
            setFilterTags(filterTags.filter(x => x !== tag));
        }
    }

    let notes = data.notes || [];
    let tags: {[key: string]: number} = {};

    for(const tag of filterTags) {
        notes = notes.filter(x => x.tags.split(' ').indexOf(tag) !== -1);
    }

    if (!showNotesFor) {
        notes = notes.filter(x => !x.noteFor);
    }

    if (showNoContext) {
        notes = notes.filter(x => !x.contextId);
    }

    notes = notes.filter(x => !x.contextId || inactiveContexts.indexOf(x.contextId) === -1)

    if (searchResult) {
        notes = searchResult.map(r => {
            let idx =  notes.findIndex(n => n.id === r.id);
            return notes[idx];
        }).filter(n => n);
    }

    for (const note of notes) {

        if(!note.tags) {
            continue;
        }

        for(const tag of note.tags.split(' ')) {
            if(!tags[tag]) {
                tags[tag] = 0;
            }

            tags[tag] += 1;
        }
    }

    const tagCounts = Object.keys(tags).map(k => { return { key: k, count: tags[k], active: filterTags.indexOf(k) !== -1 }; }).sort((a, b) => b.count - a.count);

    let source: string|null = null;
    if (window.location.search.length) {
        const urlParams = new URLSearchParams(window.location.search);
        source = `?source=${encodeURIComponent(`${window.location.pathname}?${urlParams.toString()}`)}`
    }

    if(!virtualReferences) {
        return <Loader />;
    }

    interface NoteOutput {
        id: string|null;
        title: string;
        tags: string|null;
        contextId: string|null;
        updatedAt: Date;
        url: string;
        isRef: boolean;
    }

    const notesOutput: NoteOutput[] = notes.map(n => ({
        id: n.id,
        title: getNoteTitle(n),
        tags: n.tags,
        contextId: n.contextId,
        updatedAt: new Date(n.updatedAt),
        url: `/notes/${n.id}${source ? source : ''}`,
        isRef: false
    }));

    let virtualReferencesOutput: NoteOutput[] = [];
    
    if(!searchResult) {
        virtualReferencesOutput = virtualReferences.map(v => ({
            id: null,
            title: v.title,
            tags: null,
            contextId: v.contextId,
            updatedAt: v.updatedAt,
            url: `/notes/virtual?title=${encodeURIComponent(v.title)}${v.contextId ? `&context=${v.contextId}` : ''}&source=${encodeURIComponent(`${window.location.pathname}`)}`,
            isRef: true
        }));
    }

    virtualReferencesOutput = virtualReferencesOutput.filter(x => !x.contextId || inactiveContexts.indexOf(x.contextId) === -1)

    const output = _.orderBy(notesOutput.concat(virtualReferencesOutput), o => o.updatedAt, 'desc');

    return (
        <div style={{display: 'flex', height: '100%'}}>
            {showTagsBar && <div style={{borderRight: '1px solid #666', width: 200}}>
                <List<{key: string, count: Number, active: boolean}> items={tagCounts} renderItem={i => {
                    return (
                        <div key={i.key} style={{background: i.active ? '#444' : undefined}} onClick={() => toggleTag(i.key)} className={cx('item', styles.hoverGlow)}>{i.key} <span style={{color: '#999'}}>{i.count}</span></div>
                    );
                }} />
            </div>}
            <div style={{width: '100%'}}>
                <ToolSection>
                    <SettingsCompactor alwaysVisible={() => (
                        <>
                            <NavLink to={`/notes/new`} className="button">New Note</NavLink>
                        </>
                    )}>
                        <div>
                            <input id="tags-box" type="checkbox" checked={showTagsBar} onChange={() =>setShowTagsBar(!showTagsBar)} />
                            <label htmlFor="tags-box">Show tags bar</label>
                        </div>
                        <div>
                            <input id="show-notes-for-box" type="checkbox" checked={showNotesFor} onChange={() =>setShowNotesFor(!showNotesFor)} />
                            <label htmlFor="show-notes-for-box">Show notes for</label>
                        </div>
                        <div>
                            <input id="show-no-context-box" type="checkbox" checked={showNoContext} onChange={() =>setShowNoContext(!showNoContext)} />
                            <label htmlFor="show-no-context-box">Show no context</label>
                        </div>
                    </SettingsCompactor>
                    <input style={{fontSize: 16, padding: '.3rem', width: '100%'}} placeholder="Search..." type="text" value={text || ''} onChange={e => { setText(e.target.value); debouncedSearch(e.target.value); }}/>
                </ToolSection>
                <div className="list">
                    {output.map((n, i) => (
                        <React.Fragment key={i}>
                            <NavLink to={n.url} className={cx('item', 'note', styles.hoverGlow)} style={{color: n.isRef ? '#aaa' : undefined}}>
                                <div className="note-title">
                                    <div>{n.title}</div>
                                </div> 
                                <div className="note-details">
                                    <TagList tags={n.tags || ''} />
                                    <div className="tags">
                                        <ContextTag contextId={n.contextId} />
                                    </div>
                                    <div className="note-date">{moment(n.updatedAt).format('ddd MMM Do HH:mm')}</div>
                                </div>
                            </NavLink>
                        </React.Fragment>
                    ))}
                </div>
            </div>
        </div>
    );
};
