import * as React from 'react';
import { NavLink } from 'react-router-dom';
import * as styles from '../../layout/styles';
import { useData } from '../../model';
import { ToolSection } from '../../framework/ToolSection';
import { Person } from '../../db/person';
import { cx } from 'emotion';

export const PeoplePage: React.FC<{}> = props => {
    const {data} = useData();

    const renderTarget = (target: Person) => {
        return (
            <NavLink key={target.id} to={`/people/${target.id}`} className={cx('item', styles.hoverGlow)}>
                <div>
                    {target.name}
                </div>
            </NavLink>
        );
    };

    return (
        <>
            <ToolSection>
                <NavLink to={`/people/new`} className="button">New Person</NavLink>
            </ToolSection>

            <div className="list">
                {data.people.sort((a, b) => a.name.localeCompare(b.name)).map(t => renderTarget(t))}
            </div>
        </>
    );
};
