import * as React from 'react';
import { NavLink } from 'react-router-dom';
import { useData } from '../model';
import { ToolSection } from '../framework/ToolSection';
import { Task, TaskState } from '../db/task';
import { ProjectState } from '../db/project';
import moment from 'moment';
import * as styles from '../layout/styles';
import { EmbeddedNoteEdit } from '../components/EmbeddedNote';
import { cx } from 'emotion';
import { Api } from '../bridge/HostBridge';

export function ShowTaskPage(props: { id: string}) {
    const {data, refresh} = useData();
    const [title, setTitle] = React.useState('');
    const [source, setSource] = React.useState('/tasks');


    React.useEffect(() => {
        const urlParams = new URLSearchParams(window.location.search);
        const task = data.tasks.find(x => x.id === props.id);
        setTitle(task!.title);
        if (urlParams.has('source')) {
            setSource(decodeURIComponent(urlParams.get('source')!));
        }
    }, [data.tasks, props.id]);

    async function setContext(task: Task, event: React.ChangeEvent<HTMLSelectElement>) {
        let contextId: string|null = event.target.value;

        if (contextId === '') {
            contextId = null;
        }

        task.contextId = contextId;

        await Api.saveTask(task);
        refresh();
    }

    async function setProject(task: Task, event: React.ChangeEvent<HTMLSelectElement>) {
        let projectId: string|null = event.target.value;

        if (projectId === '') {
            projectId = null;
        }

        task.projectId = projectId;

        await Api.saveTask(task);
        refresh();
    }

    async function setDefer(task: Task, event: React.ChangeEvent<HTMLInputElement>) {
        let value: string|null = event.target.value;

        if (value === '') {
            task.deferredUntil = null;
        } else {
            task.deferredUntil = new Date(value).toISOString();
        }

        await Api.saveTask(task);
        refresh();
    }

    async function onKeyDown(e: React.KeyboardEvent<HTMLInputElement>) {
        if (e.key === 'Enter') {
            let task = data.tasks.find(x => x.id === props.id);

            if (!task) {
                return;
            }

            if (task.title !== title) {
                task.title = title;
                await Api.saveTask(task);
                refresh();
            }
        }
    }

    async function setTaskState(state: TaskState) {
        const task = data.tasks.find(x => x.id === props.id);

        if (!task) {
            return;
        }

        if (task.state === state) {
            return;
        }

        task.state = state;
        task.stateChangedAt = new Date().toISOString();
        await Api.saveTask(task);
        refresh();
    }

    let task = data.tasks.find(x => x.id === props.id);

    if (!task) {
        return <div>Task not found.</div>
    }

    let isDirty = false;

    if (task.title !== title) {
        isDirty = true;
    }

    return (
        <>
            <ToolSection>
                <NavLink to={source} className="button">Back</NavLink>
            </ToolSection>

            <ToolSection>
                <div>Task</div>
                <input type="text" className="input is-large is-wide" style={{border: isDirty ? '2px solid orange' : undefined}} value={title} onKeyDown={e => onKeyDown(e)} onChange={e => setTitle(e.target.value)} />

                <div>State</div>
                <div className={cx(styles.horizontalSpace('.5rem'))}>
                    <div className={cx('button', task.state !== TaskState.Active ? 'inactive' : null)} onClick={() => setTaskState(TaskState.Active)}>Active</div>
                    <div className={cx('button', task.state !== TaskState.Incubated ? 'inactive' : null)} onClick={() => setTaskState(TaskState.Incubated)}>Incubated</div>
                    <div className={cx('button', task.state !== TaskState.Completed ? 'inactive' : null)} onClick={() => setTaskState(TaskState.Completed)}>Completed</div>
                </div>

                <div>Context</div>
                <select className="input is-large" value={task.contextId || ''} onChange={e => setContext(task!, e)}>
                    <option value="">Not set</option>
                    {data.contexts.map(c => (
                        <option key={c.id} value={c.id}>{c.title}</option>
                    ))}
                </select>

                <div>Project</div>
                <select className="input is-large" value={task.projectId || ''} onChange={e => setProject(task!, e)}>
                    <option value="">Not set</option>
                    {data.projects.filter(p => p.state === ProjectState.Active || p.id === task!.projectId).map(c => (
                        <option key={c.id} value={c.id}>{c.title}</option>
                    ))}
                </select>

                <div>Defer</div>
                <input type="date" className="input is-large" value={(task.deferredUntil && moment(task.deferredUntil).format('YYYY-MM-DD')) || ''} onChange={e => setDefer(task!, e)} />
            </ToolSection>

            <ToolSection>
                <EmbeddedNoteEdit targetEntity={task} type={'task'} {...props} />
            </ToolSection>
        </>
    );
} 
