import * as React from 'react';
import { NavLink } from 'react-router-dom';
import { MarkedViewer } from '../../components/MarkedViewer';
import { ReferenceViewer } from '../../components/ReferenceViewer';
import { useData } from '../../model';
import { ToolSection } from '../../framework/ToolSection';
import { useQueryState } from '../../lib/UseQueryState';
import { TagList } from '../../components/TagInput';
import { ContextTag } from '../../components/ContextTag';
import { getNoteTitle, NoteMode } from '../../db/note';
import { TreeViewer } from '../../components/TreeViewer';

export const ShowNotePage: React.SFC<{id: string}> = (props) => {
    const {data} = useData();
    const [source] = useQueryState(null, 'source');
    const note = data.notes.find(x => x.id === props.id);

    if (!note) {
        return <div>Note not found.</div>
    }

    let viewer = null;

    if (note.mode === NoteMode.Markdown) {
        viewer = <MarkedViewer markdown={note.content} contextId={note.contextId} />;
    } else {
        viewer = <TreeViewer json={note.content} contextId={note.contextId} />;
    }

    return (
        <>
            <ToolSection>
                <NavLink to={source ? decodeURIComponent(source) : '/'} className="button">Back</NavLink>
                <NavLink to={`/notes/${note.id}/edit`} className="button">Edit</NavLink>
            </ToolSection>
            <div className="panel">
                <div className="content" style={{padding: '1rem'}}>
                    <h1>{getNoteTitle(note)}</h1>
                    <div style={{display: 'flex', justifyContent: 'space-between'}}>
                        <TagList tags={note.tags} />
                        <ContextTag contextId={note.contextId} />
                    </div>
                    {viewer}
                    <ReferenceViewer title={getNoteTitle(note)} contextId={note.contextId} />
                </div>
            </div>
        </>
    );
}

export const ShowVirtualNotePage: React.SFC<{}> = (props) => {
    const [source] = useQueryState(null, 'source');

    const urlParams = new URLSearchParams(window.location.search);
    const title = urlParams.get('title');
    const contextId = urlParams.get('context');

    if(!title) {
        return <div>Note not found.</div>
    }

    return (
        <>
            <ToolSection>
                <NavLink to={source ? decodeURIComponent(source) : '/'} className="button">Back</NavLink>
                <NavLink to={`/notes/new?title=${encodeURIComponent(title)}${contextId ? `&context=${contextId}` : ''}&source=${encodeURIComponent(`${window.location.pathname}`)}`} className="button">Create</NavLink>
            </ToolSection>
            <div className="panel">
                <div className="content" style={{padding: '1rem'}}>
                    <h1>{title}</h1>
                    <div style={{display: 'flex', justifyContent: 'space-between'}}>
                        <ContextTag contextId={contextId} />
                    </div>
                    <ReferenceViewer title={title} contextId={contextId} />
                </div>
            </div>
        </>
    );
}